import React, { Component } from 'react'
import { connect } from "react-redux"
import { Button } from "antd"
import { createNewItinerary, addCollectionToCurrentItinerary } from './../store/actions/editActions'
import { loadItineraryToState } from './../store/actions/viewActions'
import { PDFExport, savePDF } from '@progress/kendo-react-pdf';
import { Droppable, DragDropContext, Draggable } from 'react-beautiful-dnd';
import DetailsModal from './Edit/DetailsModal'

import Search from './Search'
import Map from './Map'

class TestPage extends Component {
    pdfExportComponent;

    exportPDFWithComponent = () => {
        this.pdfExportComponent.save();
    }
    
    onDragEnd = (e) => {

    }

    onDragStart = (e) => {
        
    }

    render() {
        return (
            <div>
                <button className="k-button" onClick={this.exportPDFWithComponent}>Export with component</button>
                <p>{this.props.edit.currentItin ? this.props.edit.currentItin.toString() : "asdf"}</p>
                <Button onClick={() => this.props.createNewItinerary()}></Button>
                <Button onClick={() => this.props.addCollectionToCurrentItinerary()}></Button>
                <Button onClick={() => this.props.loadItineraryToState()}>Load To State</Button>

                <PDFExport ref={(component) => this.pdfExportComponent = component} paperSize="A4">
                    <Button onClick={() => this.props.addCollectionToCurrentItinerary()}></Button>
                    <Button onClick={() => this.props.loadItineraryToState()}>Load To State</Button>
                </PDFExport>

                <DragDropContext onDragEnd={this.onDragEnd} onDragStart={this.onDragStart}>
                <Search>

                </Search>
                </DragDropContext>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        createNewItinerary: () => dispatch(createNewItinerary()),
        addCollectionToCurrentItinerary: () => dispatch(addCollectionToCurrentItinerary()),
        loadItineraryToState: () => dispatch(loadItineraryToState("3H0jUKxaJ5mX0LReiGH1"))
    }
}

const mapStateToProps = (state) => ({
    edit: state.edit,
    view: state.view
})

export default connect(mapStateToProps, mapDispatchToProps)(TestPage);