import React, { Component } from 'react'
import { compose } from 'redux';
import { connect } from 'react-redux'
import { firestoreConnect, withFirestore } from 'react-redux-firebase';
import ItineraryTable from './ItineraryTable'

class MyItineraryViewer extends Component {
  constructor(props) {
    super(props)
    this.state = {
      itineraries: [],
      moreResults: true,
      loading: false,
    }
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.auth.isLoaded === false && this.props.auth.isLoaded) {
      this.loadItineraries()
    }
  }

  componentDidMount() {
    this.loadItineraries()
  }

  loadItineraries = async () => {
    if (this.state.moreResults && this.props.auth.isLoaded && !this.state.loading) {
      this.setState({ loading: true }, () => {
        let query = this.props.firestore.collection('users').doc(this.props.auth.uid).collection('itineraries').orderBy('editedAt', 'desc');

        if (this.state.next) {
          query = this.state.next
        }

        query.limit(15).get().then((snapshot) => {
          const items = snapshot.docs.map(doc => {
            let data = doc.data()
            return ({ ...data, createdAtDate: data.createdAt.toDate().getTime(), editedAtDate: data.editedAt.toDate().getTime(), id: doc.id })
          })

          if (snapshot.docs.length > 0) {
            let next = this.props.firestore.collection('users').doc(this.props.auth.uid).collection('itineraries').orderBy('editedAt').startAfter(snapshot.docs[snapshot.docs.length - 1])
            this.setState({ loading: false, itineraries: this.state.itineraries.concat(items), next: next, moreResults: snapshot.docs.length >= 15 })
          } else {
            this.setState({ loading: false, itineraries: this.state.itineraries.concat(items), next: null, moreResults: false })
          }
        })
      })
    }
  }

  render() {
    return (
      <div>
        <ItineraryTable loading={!this.props.auth.isLoaded || this.state.loading} itineraries={this.state.itineraries}></ItineraryTable>
      </div>
    )
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    ...ownProps,
    auth: state.firebase.auth,
  }
}

export default compose(
  withFirestore,
  connect(mapStateToProps, null),
)(MyItineraryViewer)