import React, { Component } from 'react'
import PageLayout from '../Core/PageLayout'
import MyItineraryViewer from './MyItineraryViewer';
import { Row, Col, Button, Icon } from 'antd'
import { createNewItinerary } from './../../store/actions/editActions'
import { connect } from 'react-redux';

class Profile extends Component {
    render() {
        return (
            <PageLayout>
                <Row type="flex" justify="center">
                    <Col xs={24} sm={24} md={22} lg={20} xl={20} xxl={20} style={{ paddingTop: "100px" }}>
                        <h1 style={{ marginBottom: "20px" }}>My Itineraries</h1>
                        <Button style={{ marginBottom: "20px" }} onClick={() => this.props.createNewItinerary()} type="primary"><Icon type="plus" />Create New Itinerary</Button>
                        <MyItineraryViewer />
                    </Col>
                </Row>
            </PageLayout>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        createNewItinerary: () => dispatch(createNewItinerary())
    }
}

export default connect(null, mapDispatchToProps)(Profile); 