import React, { Component } from 'react'
import ItineraryOverview from './ItineraryOverview'
import { connect } from 'react-redux';
import { Select, Icon, Empty, Row, Col } from 'antd'
import EventList from '../Edit/EventList';
import { stickySelect, stickyBackMenu } from './ItineraryViewer.module.scss'
import { updateCurrentCollectionViewing } from './../../store/actions/viewActions'

const Option = Select.Option;

const itinerary = {
    name: "Some Basic Name",
    collection: {},
    collectionOrder: [],
    location: null,
    tags: []
}


class ItineraryViewer extends Component {


    handleChange = (value) => {
        this.props.updateCurrentCollectionViewing(value);
    }

    render() {
        return (
            <div>
                {this.props.itinerary &&
                    <div style={{ width: "100%" }}>
                        <Row type="flex" align="middle" justify="center" className={stickyBackMenu}>
                            <Col span={22}>
                                <a onClick={() => this.props.onBack()}><Icon type="left" /><span> Back</span></a>
                            </Col>
                        </Row>
                        <Row type="flex" align="middle" justify="center">
                            <Col span={22}>
                                <ItineraryOverview itinerary={itinerary}></ItineraryOverview>
                            </Col>
                        </Row>
                        <Row type="flex" align="middle" justify="center" className={stickySelect}>
                            <Col span={22}>
                                <Select defaultValue={this.props.currentCollectionId} style={{ width: "100%" }} onChange={this.handleChange}>
                                    {
                                        this.props.itinerary.collectionOrder.map(element => {
                                            return <Option key={element} value={element}>{this.props.itinerary.collection[element].name}</Option>
                                        })
                                    }
                                </Select>
                            </Col>
                        </Row>
                        <Row type="flex" align="middle" justify="center" style={{marginTop: "1.5em"}}>
                            <Col span={22}>
                                <EventList dragging={this.props.dragging} viewOnly={true} events={this.props.itinerary.collection[this.props.currentCollectionId].events} eventOrder={this.props.itinerary.collection[this.props.currentCollectionId].eventOrder}></EventList>
                            </Col>
                        </Row>
                    </div>
                }
                {!this.props.itinerary && <Empty></Empty>}
            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        ...ownProps,
        itinerary: state.view.itinerary,
        currentCollectionId: state.view.currentCollectionId,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        updateCurrentCollectionViewing: (id) => dispatch(updateCurrentCollectionViewing(id))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ItineraryViewer)