import React, { Component } from 'react'
import { menuContainer } from './FilterMenu.module.scss'
import { Button, Row, Col, Tag, Drawer, Input } from 'antd'
const { CheckableTag } = Tag;

export default class FilterMenu extends Component {

    state = { visible: false }

    handleDrawer = () => {
        this.setState({
            visible: !this.state.visible,
        });
    };

    render() {
        return (
            <Row gutter={12} className={menuContainer} type="flex" justify="start" align={"middle"} >
                <Col><MyTag type="primary">Location</MyTag></Col>
                <Col><MyTag type="primary">Trip Length</MyTag></Col>
                <Col><MyTag type="primary">Outdoors</MyTag></Col>
                <Col><MyTag type="primary">Food</MyTag></Col>
                {this.props.drawer && <Col><Button onClick={() => this.handleDrawer()} type="default">More Tags...</Button></Col>}
                {this.props.drawer && <TagDrawer closeHandler={() => this.handleDrawer()} visible={this.state.visible}></TagDrawer>}
            </Row>
        )
    }
}

class TagDrawer extends Component {

    render() {
        let tags = Array(200).fill(200).map((i) => <Col><MyTag margin>I'm A Tag</MyTag></Col>)

        return (
            <Drawer
                title={<Input placeholder="Find Something Cool" allowClear />}
                placement={"top"}
                closable={false}
                style={{ top: '115px', overflowY: "auto" }}
                onClose={() => this.props.closeHandler()}
                visible={this.props.visible}
            >
                <div style={{ height: "143px", overflowX: "hidden", overflowY: "scroll" }}>
                    <Row gutter={12} type="flex" justify="start" align={"middle"}>{tags}</Row>
                </div>
            </Drawer>
        )
    }
}

class MyTag extends React.Component {
    state = { type: "default" };

    handleChange = () => {
        this.setState({ type: this.state.type === "default" ? "primary" : "default" });
    }

    render() {
        return <Button style={{ marginBottom: this.props.margin ? "5px" : "0px" }} {...this.props} type={this.state.type} onClick={() => this.handleChange()} />;
    }
}