import React, { Component } from 'react';
import { withGoogleMap, GoogleMap, Marker } from 'react-google-maps';
import { averageGeolocation } from './../Core/Utilities'


class Map extends Component {


    render() {
        const bounds = new window.google.maps.LatLngBounds();
        const coordinates = this.props.markers.map(model => {
            const latLng = new window.google.maps.LatLng(model.lat, model.lng);
            bounds.extend(latLng);
            return latLng;
        });

        const GoogleMapExample = withGoogleMap(props => (
            <GoogleMap
                defaultCenter={{ lat: 40.756795, lng: -73.954298 }}
                defaultZoom={13}
                ref={map => map && this.props.markers.length > 0 && map.fitBounds(bounds)}
            >

                {this.props.markers && this.props.markers.map((coord, index) => {
                    return (<Marker key={index}
                        position={{ lat: coord.lat, lng: coord.lng }}
                    />)
                })}

            </GoogleMap>
        ));
        return (
            <GoogleMapExample
                containerElement={<div style={{ height: `100%`, width: '100%' }} />}
                mapElement={<div style={{ height: `100%` }} />}
            />
        );
    }
};
export default Map;