const initState = {
    authError: null
}

const authReducer = (state = initState, action) => {
    console.log(action)
    switch (action.type) {
        case 'SIGNUP_ERROR':
            console.log("signup error")
            return {
                ...state,
                authError: action.err
            }
        case 'SIGNUP_SUCCESS':
            console.log("signup success")
            return {
                ...state,
                authError: null
            }
        case 'LOGIN_ERROR':
            console.log("login error")
            return {
                ...state,
                authError: action.err
            }
        case 'LOGIN_SUCCESS':
            console.log("login success")
            return {
                ...state,
                authError: null
            }
        case 'SIGNOUT_SUCCESS':
            return {
                ...state,
                authError: null
            }
        case 'SIGNOUT_ERROR':
            return {
                ...state,
                authError: action.err
            }
        default:
            return state;
    }
}

export default authReducer