import React, { Component } from 'react';
import { Menu } from 'antd';
import { menu, menuSider } from './UnauthMenu.module.scss'
import { Link } from 'react-router-dom'
const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;

export default class UnauthMenu extends Component {
    render() {
        console.log(this.props.sider)
        return (
            <Menu selectable={false} className={this.props.sider ? menuSider : menu} mode={this.props.sider ? "vertical" : "horizontal"}>
                <Menu.Item key="mail">
                    <Link to="/login">Login</Link>
                </Menu.Item>
                <Menu.Item key="app">
                    <Link to="/signup">Signup</Link>
                </Menu.Item>
            </Menu>
        );
    }
}
