import React, { Component } from 'react'
import { Droppable, DragDropContext, Draggable } from 'react-beautiful-dnd';
import EventItem from './EventItem';
import { Timeline, Divider, Button, Icon, Menu } from 'antd'
import { addSection } from './EventList.module.scss'
import { connect } from 'react-redux';
import { addNewEventToCurrentCollection, updateEventOrder, prepCurrentEventEdit } from './../../../store/actions/editActions'
import { guidGenerator } from './../../Core/Utilities'

const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);

    return result;
};

class EventList extends Component {

    constructor(props) {
        super(props);
        this.state = {
            events: (props.collection && props.collection.events) ? props.collection.events : {},
            eventOrder: (props.collection && props.collection.eventOrder) ? props.collection.eventOrder : [],
            editing: null,
            dragging: false,
        }
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.collection && (nextProps.collection.events !== this.props.events || nextProps.collection.eventOrder !== this.props.eventOrder)) {
            this.setState({
                events: nextProps.collection.events,
                eventOrder: nextProps.collection.eventOrder
            })
        }
    }

    addSectionClick = (itemBefore) => {
        let guid = guidGenerator()
        let items = {
            ...this.props.items,
            [guid]: { id: guid }
        }

        if (!itemBefore) {
            this.setState({ eventOrder: [guid], events: items }, () => this.props.addNewEventToCurrentCollection(guid, [guid]));
        } else {
            let index = this.props.eventOrder.indexOf(itemBefore);
            let newOrder = [...this.props.eventOrder].slice(0, index + 1).concat([guid]).concat([...this.props.eventOrder].slice(index + 1))
            this.setState({ eventOrder: newOrder, events: items }, () => this.props.addNewEventToCurrentCollection(guid, newOrder));
        }
    }

    onClick = (item) => {
        this.setState({ editing: item })
    }

    render() {
        return (
            <Droppable droppableId={this.props.viewOnly ? "viewPanel" : "editPanel"} isDropDisabled={this.props.viewOnly}>
                {(provided, snapshot) => (
                    <div
                        style={{ width: "100%", minHeight: "95%" }}
                        ref={provided.innerRef}
                        className={"ant-timeline"}>

                        {this.props.collectionID && this.props.eventOrder.length < 1 &&
                            <div style={{ textAlign: "center", marginTop: "25vh" }}>
                                <h1>Nothing's Planned Yet, Start Building!</h1>
                                <div onClick={() => this.addSectionClick()}>
                                    <Divider style={{ marginTop: 5, marginBottom: 5 }}>
                                        <Button type="primary" size="small" shape="circle" icon="plus" />
                                    </Divider>
                                </div>
                            </div>
                        }
                        {this.props.eventOrder && this.props.eventOrder.map((eventID, index) => (
                            <Draggable
                                key={this.props.viewOnly ? eventID + "-view" : eventID}
                                draggableId={this.props.viewOnly ? eventID + "-view" : eventID}
                                index={index}
                                type="DEFAULT"
                            >
                                {(provided, snapshot) => (
                                    <div ref={provided.innerRef}
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}>
                                        <li className={this.props.eventOrder.length - 1 === index ? "ant-timeline-item ant-timeline-item-last" : "ant-timeline-item"}
                                            style={{ padding: 0 }}
                                        >
                                            {!this.props.dragging && <div className={"ant-timeline-item-tail"} />}
                                            {!this.props.dragging && <div className={"ant-timeline-item-head ant-timeline-item-head-blue"} />}
                                            <div className={"ant-timeline-item-content"}>
                                                <div onClick={() => { if (this.props.editing !== eventID && !this.props.viewOnly) { this.props.prepCurrentEventEdit(eventID, this.props.events[eventID]) } }} >
                                                    <EventItem editing={this.props.editing === eventID} item={this.props.events[eventID]} id={eventID} />
                                                </div>
                                                {!this.props.viewOnly &&
                                                    <div onClick={() => this.addSectionClick(eventID)} className={addSection}>
                                                        <Divider style={{ marginTop: 5, marginBottom: 5 }}>
                                                            <Button type="primary" size="small" shape="circle" icon="plus" />
                                                        </Divider>
                                                    </div>
                                                }
                                                {this.props.viewOnly && <div style={{ height: 25 }}></div>}
                                            </div>
                                        </li>
                                    </div>
                                )}
                            </Draggable>
                        ))}
                        {provided.placeholder}
                    </div>
                )}
            </Droppable>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        addNewEventToCurrentCollection: (id, order) => dispatch(addNewEventToCurrentCollection(id, order)),
        updateEventOrder: (newOrder) => dispatch(updateEventOrder(newOrder)),
        prepCurrentEventEdit: (id, event) => dispatch(prepCurrentEventEdit(id, event))
    }
}

const mapStateToProps = (state) => ({
    editing: state.edit.editEventID
})

export default connect(mapStateToProps, mapDispatchToProps)(EventList)
