import React, { Component } from 'react'
import { Upload, Icon, Modal } from 'antd';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withFirebase } from 'react-redux-firebase'
import { guidGenerator } from './../Core/Utilities'

class PictureUpload extends React.Component {

    constructor(props) {
        super(props);

        let temp = [];
        this.props.fileList.forEach(obj => temp.push({ ...obj }))

        this.state = {
            previewVisible: false,
            previewImage: '',
            fileList: temp,
        };
    }

    handleCancel = () => this.setState({ previewVisible: false })

    handlePreview = (file) => {
        this.setState({
            previewImage: file.url || file.thumbUrl,
            previewVisible: true,
        });
    }

    updateFileState = (file, percent, status, url, update) => {
        let files = [...this.state.fileList]
        let index = this.state.fileList.findIndex((f) => {
            return f.uid === file.uid
        })

        let updatedFile = {
            ...this.state.fileList[index]
        };

        updatedFile.percent = percent ? parseFloat(percent) : parseFloat(updatedFile.percent)
        updatedFile.status = status ? status : updatedFile.status
        updatedFile.url = url ? url : updatedFile.url

        files[index] = updatedFile;
        this.setState({ fileList: files }, () => {
            if (update) {
                let tempList = [...this.state.fileList]
                this.props.handleUpdate(tempList.map((obj) => { return { url: obj.url, name: obj.name, uid: obj.uid, status: obj.status } }))
            }
        })
    }

    handleUpload = (item) => {
        var storageRef = this.props.firebase.storage().ref();
        var userRef = storageRef.child(this.props.auth.uid);
        var imageRef = userRef.child('images/' + guidGenerator());

        let uploadTask = imageRef.put(item.file)

        let that = this
        uploadTask.on('state_changed', function (snapshot) {
            var progress = Math.round(snapshot.bytesTransferred / snapshot.totalBytes * 100).toFixed(2);
            that.updateFileState(item.file, progress, null, null, null, false)
        }, function (error) {
            // Handle unsuccessful uploads
        }, function () {
            uploadTask.snapshot.ref.getDownloadURL().then(function (downloadURL) {
                that.updateFileState(item.file, null, 'done', downloadURL, true)
            });
        });
    }

    handleChange = ({ fileList }) => this.setState({ fileList }, console.log(this.state))

    render() {
        const { previewVisible, previewImage, fileList } = this.state;
        const uploadButton = (
            <div>
                <Icon type="plus" />
                <div className="ant-upload-text">Upload</div>
            </div>
        );
        return (
            <div className="clearfix">
                <Upload
                    action="//jsonplaceholder.typicode.com/posts/"
                    listType="picture-card"
                    fileList={fileList}
                    onPreview={this.handlePreview}
                    onChange={this.handleChange}
                    customRequest={this.handleUpload}
                >
                    {fileList.length >= 3 ? null : uploadButton}
                </Upload>
                <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
                    <img alt="example" style={{ width: '100%' }} src={previewImage} />
                </Modal>
            </div>
        );
    }
}




const mapStateToProps = (state) => ({
    auth: state.firebase.auth
})

export default compose(withFirebase, connect(mapStateToProps, null))(PictureUpload);