const initState = {
    currentRef: null,
    currentCollection: null,
    currentItinID: null,
    editEventID: null,
    editError: null,
    currentEventEditing: null,
    currentEventInitial: null,
    showNewModal: false,
    saving: false
}

const editReducer = (state = initState, action) => {

    console.log(action.type)

    switch (action.type) {
        case "CLEAR_STATE":
            return {
                ...initState
            }
        case "START_SAVING":
            return {
                ...state,
                saving: true
            }
        case "PREP_EDIT_STATE":
            return {
                ...state,
                editError: null,
                currentItinID: action.itinID
            }
        case 'NEW_ITIN_CREATED_SUCCESS':
            return {
                ...initState,
                currentItinID: action.itinID,
                currentRef: action.itinRef,
                showNewModal: true,
            }
        case 'CLOSE_NEW_MODAL':
            return {
                ...state,
                showNewModal: false
            }
        case "NEW_COLLECTION_SUCCESS":
            return {
                ...state,
                saving: false,
                editError: null,
            }
        case "UPDATE_CURRENT_COLLECTION_VIEW":
            return {
                ...state,
                currentCollection: action.currentCollection
            }
        case "LOAD_ITIN_SUCCESS":
            return {
                ...state,
                currentRef: action.itinRef,
                currentItinID: action.itinID,
            }
        case "UPDATE_COLLECTION_SELECTED_SUCCESS":
            return {
                ...state,
                currentCollection: action.collectionID,
                editEventID: null,
            }
        case "PREP_CURRENT_EVENT_EDITING":
            return {
                ...state,
                editEventID: action.id,
                currentEventEditing: action.event,
                currentEventInitial: action.event
            }
        case "UPDATE_CURRENT_EVENT_EDITING":
            return {
                ...state,
                currentEventEditing: action.event
            }
        case "CURRENT_EVENT_SAVED_ERROR":
            return {
                ...state,
                editError: action.error
            }
        case "CURRENT_EVENT_SAVED_SUCCESS":
            return {
                ...state,
                editError: action.error,
                editEventID: null,
                currentEventEditing: null,
                currentEventInitial: null,
                saving: false,
            }
        case "CLOSE_EVENT_EDITING":
            return {
                ...state,
                editEventID: null,
                currentEventEditing: null,
                currentEventInitial: null,
                saving: false,
            }
        case "PREP_CURRENT_EVENT_EDITING_SPECIFIC":
            return {
                ...state,
                currentCollection: action.collectionId,
                editEventID: action.id,
                currentEventEditing: action.event,
                currentEventInitial: action.event
            }
        case "NEW_EVENT_SUCCESS":
            return {
                ...state,
                editEventID: action.id,
                currentEventEditing: action.event,
                currentEventInitial: action.event,
                saving: false
            }
        case "ADD_EVENT_SUCCESS":
            return {
                ...state,
                editEventID: action.id,
                currentEventEditing: action.event,
                currentEventInitial: action.event,
                saving: false
            }
        case "UPDATED_EVENTLIST_ORDER_SUCCESS":
            return {
                ...state,
                saving: false
            }
        case "UPDATED_COLLECTION_ORDER_SUCCESS":
            return {
                ...state,
                saving: false
            }
        case "CURRENT_EVENT_SAVED_SUCCESS":
            return {
                ...state,
                saving: false
            }
        case "ITIN_UPDATE_SUCCESS":
            return {
                ...state,
                saving: false
            }
        default:
            return state;
    }
}

export default editReducer