import { Card, Col, Icon, Menu, Row, Modal } from 'antd';
import React, { Component } from 'react';
import ReactQuill from 'react-quill';
import { connect } from "react-redux";
import PictureUpload from './../../PictureUpload/PictureUpload';
import { cardEdit, menu, section, titleInput } from './EventItem.module.scss';
import TagGroup from './TagGroup';
import { updateCurrentEvent, saveCurrentEventEdit, deleteEventFromCollection } from './../../../store/actions/editActions'
import EstablishmentSearch from '../../EstablishmentSearch';
import Multisearch from '../../Multisearch';

const confirm = Modal.confirm;

class EventItemEdit extends Component {

    constructor(props) {
        super(props);
        this.state = {
            text: "",
            selected: ["location", "description", "tags", "images"],
            location: true,
            note: true,
            picture: true,
            tag: true,
            item: {
                ...props.item
            }
        }
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.item && nextProps.item !== this.state.item) {
            this.setState({ item: nextProps.item }, () => this.props.updateCurrentEvent(this.state.item))
        }
    }

    handlePictureChange = (fileList) => {
        this.setState({
            item: {
                ...this.state.item,
                images: fileList
            }
        }, () => this.props.updateCurrentEvent(this.state.item));
    }

    handleNameChange = (e) => {
        this.setState({
            item: {
                ...this.state.item,
                name: e.target.value
            }
        }, () => this.props.updateCurrentEvent(this.state.item));
    }

    handleTagChange = (tags) => {
        this.setState({
            item: {
                ...this.state.item,
                tags: tags
            }
        }, () => this.props.updateCurrentEvent(this.state.item))
    }

    handleLocationChange = (place) => {
        this.setState({
            item: {
                ...this.state.item,
                location: {
                    place_id: place.id,
                    name: place.description,
                    lat: place.lat,
                    lng: place.lng
                }
            }
        }, () => this.props.updateCurrentEvent(this.state.item));
    }

    handleDetailChange = (value) => {
        this.setState({
            item: {
                ...this.state.item,
                description: value
            }
        }, () => this.props.updateCurrentEvent(this.state.item))
    }

    updateMenuState = (element) => {
        let newState = [...this.state.selected];
        let index = this.state.selected.indexOf(element);

        if (index === -1) {
            newState.push(element);
        } else {
            newState = [...this.state.selected.slice(0, index)].concat(...this.state.selected.slice(index + 1))
        }

        this.setState({ selected: newState })
    }

    onMenuClick = (menuElement) => {
        switch (menuElement) {
            case "location":
                this.state.item.location ? this.showConfirm("Remove location from event?", menuElement) : this.updateMenuState(menuElement); break;
            case "description":
                this.state.item.description ? this.showConfirm("Remove notes from event?", menuElement) : this.updateMenuState(menuElement); break;
            case "images":
                this.state.item.images ? this.showConfirm("Remove image(s) from event?", menuElement) : this.updateMenuState(menuElement); break;
            case "tags":
                this.state.item.tags ? this.showConfirm("Remove tag(s) from event?", menuElement) : this.updateMenuState(menuElement); break;
        }
    }

    showConfirm = (title, element) => {
        let currentItem = this.state.item
        let that = this
        confirm({
            title: title,
            okText: 'Yes',
            cancelText: 'No',
            onOk() {
                let item = { ...that.state.item }
                delete item[element]
                that.setState({
                    item: item
                }, () => { that.props.updateCurrentEvent(that.state.item); that.updateMenuState(element) })
            },
            onCancel() {
                return false;
            },
        });
    }

    onDeleteClick = (id) => {
        let that = this
        confirm({
            title: "Are you sure you want to delete this event?",
            okText: 'Yes',
            cancelText: 'No',
            onOk() {
                that.props.deleteEventFromCollection(that.props.collectionID, id)
            },
            onCancel() {
                return false;
            },
        });
    }

    render() {
        return (
            <div>
                <Row type="flex" justify="space-between" style={{ alignItems: "flex-start" }}>
                    <Col span={1} style={{ flexGrow: 1 }} >
                        <Card className={cardEdit} style={{ minHeight: 294 }} bordered={false}>
                            <input value={this.state.item.name} onChange={this.handleNameChange} className={titleInput}></input>

                            {this.state.selected.includes("location") &&

                                <Row className={section} type="flex" align="middle" justify="start">
                                    <Col span={1}>
                                        <Icon type="environment" />
                                    </Col>
                                    <Col span={23}>
                                        <EstablishmentSearch value={this.state.item && this.state.item.location && this.state.item.location.name ? this.state.item.location.name : ""} onSelect={(place) => this.handleLocationChange(place)} style={{ display: "inline" }}></EstablishmentSearch>
                                    </Col>
                                </Row>
                            }

                            {this.state.selected.includes("description") &&
                                <div className={section}>
                                    <ReactQuill style={{ height: "300px", paddingTop: "20px", paddingBottom: "50px" }} value={this.state.item.description ? this.state.item.description : ""}
                                        onChange={this.handleDetailChange} />
                                </div>
                            }

                            {this.state.selected.includes("images") &&
                                <div className={section}>
                                    <PictureUpload fileList={this.state.item && this.state.item.images ? this.state.item.images : []} handleUpdate={(fileList) => this.handlePictureChange(fileList)}></PictureUpload>
                                </div>}

                            {this.state.selected.includes("tags") &&
                                <div className={section}>
                                    <TagGroup tags={this.state.item && this.state.item.tags ? this.state.item.tags : []} handleTagChange={(tags) => this.handleTagChange(tags)}></TagGroup>
                                </div>
                            }
                        </Card>
                    </Col>
                    <Col style={{ minWidth: "30px" }}>
                        <div style={{ paddingLeft: 10 }}>
                            <Row style={{ marginBottom: 10 }} className={cardEdit}>
                                <Menu theme="dark" mode="inline" selectable={false} style={{ borderRight: 0, backgroundColor: "white" }}>
                                    <Menu.Item style={{ color: "red" }} onClick={() => this.onDeleteClick(this.props.id)}>
                                        <Icon type="close" />
                                    </Menu.Item>
                                </Menu>
                            </Row>
                            <Row className={cardEdit}>
                                <Menu theme="dark" selectedKeys={this.state.selected} mode="inline" multiple className={menu} style={{ borderRight: 0, backgroundColor: "white" }}>
                                    <Menu.Item key="location" style={{ margin: 0, color: this.state.selected.includes("location") ? "white" : "rgba(0, 0, 0, 0.65)" }} onClick={() => this.onMenuClick("location")}>
                                        <Icon type="pushpin" />
                                    </Menu.Item >
                                    <Menu.Item key="description" style={{ margin: 0, color: this.state.selected.includes("description") ? "white" : "rgba(0, 0, 0, 0.65)" }} onClick={() => this.onMenuClick("description")}>
                                        <Icon type="file-text" />
                                    </Menu.Item>
                                    <Menu.Item key="images" style={{ margin: 0, color: this.state.selected.includes("images") ? "white" : "rgba(0, 0, 0, 0.65)" }} onClick={() => this.onMenuClick("images")}>
                                        <Icon type="picture" />
                                    </Menu.Item>
                                    <Menu.Item key="tags" style={{ margin: 0, color: this.state.selected.includes("tags") ? "white" : "rgba(0, 0, 0, 0.65)" }} onClick={() => this.onMenuClick("tags")}>
                                        <Icon type="tag" />
                                    </Menu.Item>
                                </Menu>
                            </Row>
                            <Row style={{ marginTop: 10 }} className={cardEdit}>
                                <Menu theme="dark" mode="inline" selectable={false} style={{ borderRight: 0, backgroundColor: "#1890ff" }}>
                                    <Menu.Item style={{ color: "white" }} onClick={() => this.props.saveCurrentEventEdit()}>
                                        <Icon type="check" />
                                    </Menu.Item>
                                </Menu>
                            </Row>
                        </div>
                    </Col>


                </Row>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => ({
    updateCurrentEvent: (event) => dispatch(updateCurrentEvent(event)),
    saveCurrentEventEdit: () => dispatch(saveCurrentEventEdit()),
    deleteEventFromCollection: (collectId, eventId) => dispatch(deleteEventFromCollection(collectId, eventId))
})


const mapStateToProps = (state) => ({
    collectionID: state.edit.currentCollection
})

export default connect(mapStateToProps, mapDispatchToProps)(EventItemEdit)