const newItinerary = {
    name: "Untitled",
    location: [],
    tags: [],
    collection: {},
    collectionOrder: []
}

const newCollection = {
    name: "Untitled",
    events: {},
    eventOrder: []
}

const newEvent = {
    name: "Untitled"
}

const generateNewCollection = () => {
    let guid = generateGuid()

    let tempCollection = {
        name: "Untitled",
        events: {},
        eventOrder: []
    }
    tempCollection.eventOrder.push(guid);
    tempCollection.events[guid] = {
        ...newEvent
    }
    return tempCollection;
}

const generateGuid = () => {
    var S4 = function () {
        return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
    };
    return (S4() + S4() + "-" + S4() + "-" + S4() + "-" + S4() + "-" + S4() + S4() + S4());
}

export const prepEditState = (id) => {
    return (dispatch, getState, { getFirestore }) => {
        dispatch({ type: "PREP_EDIT_STATE", itinID: id });
    }
}

export const loadItineraryToEdit = (id) => {
    return (dispatch, getState, { getFirestore }) => {

        dispatch({ type: "START_SAVING" })
        const { firebase } = getState();
        const firestore = getFirestore();

        let itinRef = firestore.collection("users").doc(firebase.auth.uid).collection("itineraries").doc(id);

        dispatch({
            type: "LOAD_ITIN_SUCCESS", itinID: id, itinRef: itinRef, transition: (prevState, nextState, action) => ({
                pathname: `/edit/` + id
            })
        })
    }
}

export const resetNewModal = () => {
    return (dispatch, getState, { getFirestore }) => {
        dispatch({ type: "CLOSE_NEW_MODAL" })
    }
}
export const createNewItinerary = () => {
    return (dispatch, getState, { getFirestore }) => {

        dispatch({ type: "START_SAVING" })
        const { firebase, edit } = getState();
        const firestore = getFirestore();
        dispatch({ type: "CLEAR_STATE" })

        let newDocRef = firestore.collection("users").doc(firebase.auth.uid).collection("itineraries").doc();

        newDocRef.set({ ...newItinerary, editedAt: firestore.FieldValue.serverTimestamp(), createdAt: firestore.FieldValue.serverTimestamp() }).then(() => {
            dispatch({
                type: "NEW_ITIN_CREATED_SUCCESS", itinID: newDocRef.id, itinRef: newDocRef, meta: {
                    transition: (prevState, nextState, action) => ({
                        pathname: `/edit/` + newDocRef.id
                    })
                }
            })
        }).catch(e => {
            dispatch({ type: "NEW_ITIN_CREATED_ERROR", e })
        });
    }
}

