export const saveCurrentEventEdit = () => {
    return (dispatch, getState, { getFirestore }) => {

        dispatch({ type: "START_SAVING" });
        const state = getState();

        if (state.edit.currentEventEditing) {
            const firestore = getFirestore();

            let itinRef = firestore.collection("users").doc(state.firebase.auth.uid).collection("itineraries").doc(state.edit.currentItinID);

            if (JSON.stringify(state.edit.currentEventEditing) !== JSON.stringify(state.edit.currentEventInitial)) {
                itinRef.set({
                    editedAt: firestore.FieldValue.serverTimestamp(),
                    collection: {
                        [state.edit.currentCollection]: {
                            events: {
                                [state.edit.editEventID]: state.edit.currentEventEditing
                            }
                        }
                    }
                }, { merge: true }).then(() => {
                    dispatch({ type: "CURRENT_EVENT_SAVED_SUCCESS" })
                }).catch((e) => {
                    dispatch({ type: "CURRENT_EVENT_SAVED_ERROR", e })
                })
            } else {
                dispatch({ type: "CLOSE_EVENT_EDITING" })
            }
        } else {
            dispatch({ type: "EVENT_SETUP_ERROR" })
        }
    }
}

export const makeItineraryPublic = () => {
    return (dispatch, getState, { getFirestore }) => {
        const state = getState();
        const firestore = getFirestore();

        let itinRef = firestore.collection("users").doc(state.firebase.auth.uid).collection("itineraries").doc(state.edit.currentItinID);
        let publicRef = firestore.collection("publicItineraries").doc()

        itinRef.get().then((doc) => {
            if (doc.exists) {
                let itin = doc.data();
                publicRef.set(itin).then(() => {
                    dispatch({ type: "ITIN_TO_PUBLIC_SUCCESS" })
                }).catch((e) => {
                    dispatch({ type: "ITIN_TO_PUBLIC_ERROR", e })
                })
            }
        }).catch(e => {
            dispatch({ type: "ITIN_TO_PUBLIC_ERROR", e })
        })
    }
}