import { Icon, Menu } from 'antd';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import Footer from '../Core/Footer';
import Discover from '../Discover';
import Profile from '../Profile/MyItineraryViewer';
import Buckets from '../Buckets';
import { addToPublicIndex, getPublicQueryResults } from './../../store/actions/queryActions';
import { siderClosed, siderOpen } from './Dashboard.module.scss';
import PageLayout from '../Core/PageLayout.js';


class Dashboard extends Component {
    state = {
        collapsed: false,
        component: 1,
    }

    onCollapse = (collapsed) => {
        this.setState({ collapsed });
    }

    onMenuClick = (comp) => {
        this.setState({ component: comp });
    }

    render() {

        let toRender = null;

        switch (this.state.component) {
            case 1:
                toRender = <Profile />
                break
            case 2:
                toRender = <Discover />
                break
            case 3:
                toRender = <Discover />
                break
            case 4:
                toRender = <Buckets />
                break
        }

        return (
            <PageLayout>
                
            </PageLayout>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        addToPublicIndex: (newItin) => dispatch(addToPublicIndex(newItin)),
        getPublicQueryResults: (coordinates) => dispatch(getPublicQueryResults(coordinates))
    }
}
export default connect(mapDispatchToProps)(Dashboard);