import React, { Component } from 'react';
import AuthMenu from './AuthMenu'
import UnauthMenu from './UnauthMenu'
import { Drawer, Col, Button, Row, Icon } from 'antd';
import { Link } from 'react-router-dom'
import { withRouter } from "react-router";
import { connect } from 'react-redux';
import { logo, logoIcon, logoText, iconButton, menuBar, searchContainer, menuCon, right, hiddenSmall } from './Navbar.module.scss'
import kangaroo from './static/kangaroo.svg'
import SearchInput from '../../SearchInput'
import { getPublicQueryResults } from '../../../store/actions/queryActions';

class Navbar extends Component {
    state = {
        current: 'mail',
        visible: false
    }
    showDrawer = () => {
        this.setState({
            visible: true,
        });
    };
    onClose = () => {
        this.setState({
            visible: false,
        });
    };

    render() {
        return (
            <nav className={menuBar}>
                <Row type="flex" justify="start" align="middle">
                    <Col style={{ display: 'inline-flex' }}>
                        <div className={logo}>
                            <Link to="/" className={logoText}>
                                <img src={kangaroo} className={logoIcon} alt="Travelroo Logo"></img>
                                Travelroo
                            </Link>
                        </div>
                    </Col>
                    <Col className={searchContainer} sm={0} xs={0} md={1} style={{ maxWidth: "40%", flexGrow: 1 }}>
                        <SearchInput reroute={true}></SearchInput>
                    </Col>
                    <Col style={{ display: 'inline-flex', marginLeft: "auto" }}>
                        <Row>
                            <Col sm={0} xs={0} md={24} lg={24} xl={24} className={menuCon}>
                                {!this.props.auth.uid && <UnauthMenu />}
                                {this.props.auth.uid && <AuthMenu auth={this.props.auth} />}
                            </Col>
                            <Col sm={24} xs={24} md={0} className={menuCon}>
                                <Button onClick={() => this.showDrawer()} className={iconButton}><Icon type="menu-fold" /></Button>
                            </Col>

                            <Drawer
                                title="Travelroo.io"
                                placement="right"
                                closable={false}
                                onClose={() => this.onClose()}
                                visible={this.state.visible}
                                style={{ zIndex: 3000 }}
                            >
                                {!this.props.auth.uid && <UnauthMenu sider={true} />}
                                {this.props.auth.uid && <AuthMenu auth={this.props.auth} sider={true} />}
                            </Drawer>
                        </Row>
                    </Col>
                </Row>
            </nav>
        );
    }
}

const mapStateToProps = (state) => ({
    auth: state.firebase.auth
})

const mapDispatchToProps = (dispatch) => {
    return {
        getPublicQueryResults: (coordinates) => dispatch(getPublicQueryResults(coordinates))
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Navbar));