import React, { Component } from 'react';
import PlacesAutocomplete, {
    geocodeByAddress,
    geocodeByPlaceId,
    getLatLng,
} from 'react-places-autocomplete';

import { Input, Button } from 'antd';

const Search = Input.Search;

const searchOptions = {
    types: ['establishment'],
    fields: ['place_id', 'name']
}

export default class EstablishmentSearch extends Component {
    constructor(props) {
        super(props);
        this.state = { address: this.props.value };
    }

    handleChange = address => {
        this.setState({ address: address, selected: false });
    };

    handleSelect = (place) => {
        this.setState({ address: place.description, selected: true })

        geocodeByPlaceId(place.placeId)
            .then(results => getLatLng(results[0]))
            .then(latLng => this.props.onSelect({...place, ...latLng}))
            .catch(error => console.error('Error', error));
    };

    render() {

        let styleActive = {
            boxShadow: "0 12px 20px #d8e0e6",
            border: "solid 1px #e8e8e8",
            position: "absolute",
            zIndex: 2000,
            width: "100%"
        }

        let styleUnactive = {
            boxShadow: "none",
            border: "none",
            position: "absolute",
            zIndex: 2000,
            width: "100%"
        }

        return (
            <PlacesAutocomplete
                value={this.state.address}
                onChange={this.handleChange}
                searchOptions={searchOptions}
                debounce={350}
            >
                {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
                    <div>
                        <Input
                            {...getInputProps({
                                placeholder: 'Search Places ...',
                                className: 'location-search-input',
                            })}
                            placeholder="Location"
                            allowClear
                        />
                        <div className="autocomplete-dropdown-container" style={loading || suggestions.length > 0 ? styleActive : styleUnactive}>
                            {loading && <div>Loading...</div>}
                            {!this.state.selected && suggestions.map(suggestion => {
                                const className = suggestion.active
                                    ? 'suggestion-item--active'
                                    : 'suggestion-item';
                                // inline style for demonstration purpose
                                const style = suggestion.active
                                    ? { backgroundColor: '#e6f7ff', cursor: 'pointer', padding: '.5em', fontWeight: "bold" }
                                    : { backgroundColor: '#ffffff', cursor: 'pointer', padding: '.5em' };
                                return (
                                    <div
                                        {...getSuggestionItemProps(suggestion, {
                                            className,
                                            style,
                                        })}
                                        onClick={() => this.handleSelect(suggestion)}
                                    >
                                        <span>{suggestion.description}</span>
                                    </div>
                                );
                            })}
                        </div>
                    </div>
                )}
            </PlacesAutocomplete>
        );
    }
}