import { Button, Modal, Steps } from 'antd';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { updateCurrentItineraryLocation, updateCurrentItineraryName, updateCurrentItineraryTags } from './../../../store/actions/editActions';
import DetailsPanel from './DetailsPanel';
import NamePanel from './NamePanel';


const Step = Steps.Step;

class CreateModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            current: 0,
            name: this.props.itinerary && this.props.itinerary.name ? this.props.itinerary.name : "",
            locations: this.props.itinerary && this.props.itinerary.location ? this.props.itinerary.location : [{}],
            tags: this.props.itinerary && this.props.itinerary.tags ? this.props.itinerary.tags : [],
            detailError: ""
        };
    }

    componentWillReceiveProps(nextProps) {
        if (JSON.stringify(nextProps.itinerary) !== JSON.stringify(this.props.itinerary)) {
            let nextState = {
                detailError: "",
                name: nextProps.itinerary && nextProps.itinerary.name && nextProps.itinerary.name !== this.state.name ? nextProps.itinerary.name : this.state.name,
                locations: nextProps.itinerary && nextProps.itinerary.location && nextProps.itinerary.location !== this.state.location ? nextProps.itinerary.location : this.state.location,
                tags: nextProps.itinerary && nextProps.itinerary.tags && nextProps.itinerary.tags !== this.state.tags ? nextProps.itinerary.tags : this.state.tags,
            }
            this.setState({ nextState })
        }
    }

    handleTagChange = (tags) => {
        this.setState({ tags })
    }

    handleOk = (e) => {
        this.setState({
            visible: false,
        });
    }

    handleCancel = (e) => {
        this.setState({
            visible: false,
        });
    }

    handleLocationChange = (location) => {
        this.setState({ locations: location })
    }

    purgeLocations = () => {
        let locations = [...this.state.locations]
        let purge = []


        locations.forEach((location) => {
            if (location.name) {
                purge.push(location)
            }
        })
        return purge
    }

    handleNameChange = (e) => {
        this.setState({ name: e.target.value });
    }

    next() {
        switch (this.state.current) {
            case 0:
                if (this.state.name.trim().length > 0) {
                    this.state.name.localeCompare(this.props.itinerary.name) !== 0 && this.props.updateCurrentItineraryName(this.state.name);
                }
                break;
            case 1:
                if (this.state.location !== null) {
                    let temp = this.purgeLocations()
                    this.props.updateCurrentItineraryLocation(temp);
                }
                if (this.state.tags.length > 0) {
                    if ((this.props.itinerary.tags && this.props.itinerary.tags !== this.state.tags) || !this.props.itinerary.tags && this.state.tags) {
                        this.props.updateCurrentItineraryTags(this.state.tags);
                    }
                }
                break; 
        }
        const current = this.state.current + 1;
        this.setState({ current });
    }

    prev() {
        switch (this.state.current) {
            case 0:
                if (this.state.name.trim().length > 0) {
                    this.props.itinerary.name !== this.state.name && this.props.updateCurrentItineraryName(this.state.name);
                }
                break;
            case 1:
                if (this.state.location !== null) {
                    if ((this.props.itinerary.location && this.props.itinerary.location !== this.state.location) || !this.props.itinerary.location && this.state.location) {
                        this.props.updateCurrentItineraryLocation(this.state.location);
                    }
                }
                if (this.state.tags.length > 0) {
                    if ((this.props.itinerary.tags && this.props.itinerary.tags !== this.state.tags) || !this.props.itinerary.tags && this.state.tags) {
                        this.props.updateCurrentItineraryTags(this.state.tags);
                    }
                }
                break;

        }
        const current = this.state.current - 1;
        this.setState({ current });
    }

    render() {
        const steps = [{
            title: 'Name',
            content: <NamePanel name={this.state.name} onChange={this.handleNameChange}></NamePanel>,
        }, {
            title: 'Details',
            content: <DetailsPanel error={this.state.detailError} tags={this.state.tags} itinerary={this.props.itinerary} handleTagChange={this.handleTagChange} handleLocationChange={(locations) => this.handleLocationChange(locations)}></DetailsPanel>,
        }, {
            title: 'Build',
            content: 'Last-content',
        }];

        const { current } = this.state;
        return (
            <Modal
                title={<Steps style={{ marginTop: "15px" }} progressDot current={current}>
                    {steps.map(item => <Step key={item.title} title={item.title} />)}
                </Steps>}
                visible={this.props.visible}
                centered
                closable={false}
                width="50vw"
                footer={[
                    <div className="steps-action">
                        {
                            current > 0
                            && (
                                <Button style={{ marginLeft: 8 }} onClick={() => this.prev()}>Previous</Button>
                            )
                        }
                        {
                            current < steps.length - 1
                            && <Button type="primary" disabled={current === 0 && this.state.name.trim().length < 1 || current === 1 && this.purgeLocations().length < 1} onClick={() => this.next()}>Next</Button>
                        }
                        {
                            current === steps.length - 1
                            && <Button type="primary" onClick={() => this.props.handleModal()}>Build!</Button>
                        }
                    </div>
                ]}
            >
                <div style={{ padding: "1em" }}>
                    <div className="steps-content" style={{ minHeight: "25vh", padding: "3em" }}>{steps[current].content}</div>
                </div>
            </Modal>

        );
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        updateCurrentItineraryName: (name) => dispatch(updateCurrentItineraryName(name)),
        updateCurrentItineraryLocation: (location) => dispatch(updateCurrentItineraryLocation(location)),
        updateCurrentItineraryTags: (tags) => dispatch(updateCurrentItineraryTags(tags))
    }
}

export default connect(null, mapDispatchToProps)(CreateModal)