import { GeoCollectionReference, GeoFirestore, GeoQuery, GeoQuerySnapshot } from 'geofirestore';
import { Geokit, LatLngLiteral } from 'geokit';


export const closeDiscoverModal = () => {
    return (dispatch, getState, { getFirestore }) => {
        dispatch({ type: "CLOSE_DISCOVER_MODAL" })
    }
}

/* 
    {
        lat: 
        lng: 
    }
*/
export const getPublicQueryResults = (query) => {

    return (dispatch, getState, { getFirestore }) => {
        const firestore = getFirestore()
        const geofirestore = new GeoFirestore(firestore);
        const geocollection = geofirestore.collection('publicIndex');

        let geoQuery = null

        if (query.type == 'location') {
            let location = query.location
            location.radius = 5
            geoQuery = geocollection.near({ center: new firestore.GeoPoint(location.lat, location.lng), radius: location.radius });
        } else {
            geoQuery = geocollection.where('user.displayName', '>=', query.user);
        }

        dispatch({ type: "QUERY_LOADING" })

        geoQuery.get().then((querySnapshot) => {
            let formatted = {};
            querySnapshot.docs.forEach((doc) => {
                formatted[doc.id] = doc.data();
            })
            dispatch({ type: "QUERY_SUCCESS", lastSnapshot: querySnapshot, formattedResults: formatted, currentQuery: null })
        }).catch(err => {
            dispatch({ type: "QUERY_ERROR", err })
        })
    }
}

export const querySpecificPublicDocument = (docId) => {

    console.log(docId)

    return (dispatch, getState, { getFirestore }) => {

        const firestore = getFirestore()
        let ref = firestore.collection("publicItineraries")
        ref.doc(docId)

        dispatch({ type: "SPECIFIC_QUERY_LOADING" })

        ref.get().then((doc) => {
            if (doc.exists) {
                dispatch({ type: "SPECIFIC_QUERY_SUCCESS", focused: doc.data() })
            } else {
                throw ("Itinerary Not Found")
            }
        }).catch(err => {
            dispatch({ type: "SPECIFIC_QUERY_ERROR", err })
        })
    }
}

