import React, { Component } from 'react'
import { input, panel } from './Panel.module.scss'
import TagGroup from '../EventList/TagGroup';
import { Row, Col, Alert, Button } from 'antd'
import LocationInput from './../DetailsModal/LocationInput'

import PlacesAutocomplete, {
    geocodeByAddress,
    getLatLng,
} from 'react-places-autocomplete';

const searchOptions = {
    types: ['(cities)']
}

export default class DetailsPanel extends Component {
    constructor(props) {
        super(props);
        this.state = { locations: this.props.itinerary && this.props.itinerary.location && this.props.itinerary.location.length > 0 ? this.props.itinerary.location : [{}] };
    }

    onButtonClick = (e) => {
        this.setState({ locations: [...this.state.locations, {}] })
    }

    handleLocation = (location, index) => {
        let newList = this.state.locations.slice(0, index).concat([location]).concat(this.state.locations.slice(index + 1));
        this.setState({ locations: newList }, () => this.props.handleLocationChange(this.state.locations))
    }

    render() {

        return (
            <div className={panel}>
                <h1>Love suggestions? Help us, help you.</h1>

                <h2 style={{ marginTop: "20px" }}>Where ya heading? 🚗 🚁 ✈️ ⛵</h2>
                <div style={{ width: "100%" }}>
                    {this.state.locations.map((location, index) => {
                        return (
                            <Row type="flex" justify="start" align="middle" style={{ marginBottom: "10px" }}>
                                <Col style={{ display: 'inline-flex', flexGrow: 1 }}>
                                    <LocationInput location={location} handleLocationSelect={(location) => this.handleLocation(location, index)} />
                                </Col>
                                {this.state.locations.length > 1 &&
                                    <Col style={{ marginLeft: "10px", display: 'inline-flex' }}>
                                        <Button size="small" type="danger" ghost shape="circle" icon="minus" onClick={() => this.onButtonDeleteClick(index)} />
                                    </Col>
                                }
                            </Row>
                        )
                    })}
                </div>

                <Button type="primary" onClick={this.onButtonClick}>Add Another Location</Button>

                <h2 style={{ marginTop: "20px" }}>Tag Some Interests</h2>
                <TagGroup tags={this.props.tags} handleTagChange={(tags) => this.props.handleTagChange(tags)}></TagGroup>
                {this.props.error.length > 0 && <Alert message={this.props.error} type="error" />}
            </div>
        )
    }
}
