import React, { Component } from 'react';
import PlacesAutocomplete, {
    geocodeByAddress,
    getLatLng,
} from 'react-places-autocomplete';
import { withRouter } from "react-router";
import { connect } from 'react-redux';
import { getPublicQueryResults } from './../../store/actions/queryActions'

import { Input, Select, Row, Col, Button } from 'antd';

const Option = Select.Option;
const Search = Input.Search;

const searchOptions = {
    types: ['(cities)']
}


class SearchInput extends Component {
    constructor(props) {
        super(props);
        this.state = { input: '', searchType: 'location' };
    }

    handleChange = input => {
        this.setState({ input, location: null });
    };

    handleSelect = input => {
        geocodeByAddress(input)
            .then(results => getLatLng(results[0]))
            .then(latLng => this.setState({ input: input, location: { ...latLng, address: input } }))
            .catch(error => console.error('Error', error));
    };

    handleSubmit = () => {
        if (this.state.searchType === 'location') {
            if (!this.state.location) {
                geocodeByAddress(this.state.input)
                    .then(results => getLatLng(results[0]))
                    .then(latLng => this.setState({ address: this.state.address, location: { ...latLng, address: this.state.input } }, () => this.props.handleSubmit(this.state.location)))
                    .catch(error => console.error('Error', error));
            } else {
                this.props.getPublicQueryResults({ type: 'location', location: this.state.location })
                if(this.props.reroute) {
                    this.props.history.push('/discover');
                }
            }
        } else {
            this.props.getPublicQueryResults({ type: 'user', user: this.state.input })
            if(this.props.reroute) {
                this.props.history.push('/discover');
            }
        }
    }

    render() {

        const selectBefore = (
            <Select defaultValue={this.state.searchType} style={{ width: 100 }} onChange={(value) => this.setState({ searchType: value }, () => console.log(this.state))}>
                <Option value="location">Location</Option>
                <Option value="user">Users</Option>
            </Select>
        );

        let styleActive = {
            boxShadow: "0 12px 20px #d8e0e6",
            border: "solid 1px #e8e8e8",
            position: "absolute",
            zIndex: 2000,
            width: "100%"
        }

        let styleUnactive = {
            boxShadow: "none",
            border: "none",
            position: "absolute",
            zIndex: 2000,
            width: "100%"
        }

        return (
            <PlacesAutocomplete
                value={this.state.input}
                onChange={this.handleChange}
                onSelect={this.handleSelect}
                searchOptions={searchOptions}
                debounce={350}
            >
                {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
                    <div>
                        <Row type="flex" justify="start" align="middle">
                            <Col style={{ display: 'inline-flex', flexGrow: 1 }}>
                                <Input
                                    {...getInputProps({
                                        placeholder: 'Search Places ...',
                                        className: 'location-search-input',
                                    })}
                                    value={this.state.input}
                                    addonBefore={selectBefore}
                                    placeholder={this.state.searchType === 'location' ? "Search Locations" : "Seach Users"}
                                />
                            </Col>
                            <Col style={{ display: 'inline-flex' }}>
                                <Button style={{ display: "inline" }} disabled={!this.state.input} onClick={() => this.handleSubmit(this.state.location)} type="primary" icon="search" />
                            </Col>
                        </Row>
                        {this.state.searchType == 'location' &&
                            <div className="autocomplete-dropdown-container" style={loading || suggestions.length > 0 ? styleActive : styleUnactive}>
                                {loading && <div>Loading...</div>}
                                {suggestions.map(suggestion => {
                                    const className = suggestion.active
                                        ? 'suggestion-item--active'
                                        : 'suggestion-item';
                                    // inline style for demonstration purpose
                                    const style = suggestion.active
                                        ? { backgroundColor: '#e6f7ff', cursor: 'pointer', padding: '.5em', fontWeight: "bold" }
                                        : { backgroundColor: '#ffffff', cursor: 'pointer', padding: '.5em' };
                                    return (
                                        <div
                                            {...getSuggestionItemProps(suggestion, {
                                                className,
                                                style,
                                            })}
                                        >
                                            <span>{suggestion.description}</span>
                                        </div>
                                    );
                                })}
                            </div>
                        }
                    </div>
                )}
            </PlacesAutocomplete>
        );
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getPublicQueryResults: (query) => dispatch(getPublicQueryResults(query))
    }
}

export default withRouter(connect(null, mapDispatchToProps)(SearchInput));