import React, { Component } from 'react'
import { Icon, Button, Card, Modal } from 'antd'
import SingleDayList from './SingleDayList';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { connect } from 'react-redux';
import { updateCurrentItinerary, updateCollectionOrder, deleteSpecificCollection, addCollectionToCurrentItinerary } from './../../../store/actions/editActions'
import { multilist } from './MultidayList.module.scss'
import { arrayEquals } from "./../../Core/Utilities"

const confirm = Modal.confirm;

const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);

    return result;
};

const addToArray = (list, index, element) => {
    const result = Array.from(list)
    result.splice(index, 0, element);
    return result;
}

class MultidayList extends Component {

    constructor(props) {
        super(props);

        this.state = {
            itinerary: this.props.itinerary ? this.props.itinerary : null,
            dragging: false,
        }
    }
    onDragEnd = (result) => {

        if (result.destination) {
            if (result.destination.droppableId === "grid") {

                const items = reorder(
                    this.state.itinerary.collectionOrder,
                    result.source.index,
                    result.destination.index
                );

                if (!arrayEquals(this.state.itinerary.collectionOrder, items)) {
                    let newItin = JSON.parse(JSON.stringify(this.state.itinerary));
                    newItin.collectionOrder = items
                    this.setState({ itinerary: newItin }, () => this.props.updateCollectionOrder(items))
                }
            } else if (result.destination.droppableId === result.source.droppableId) {

                const newOrder = reorder(
                    this.state.itinerary.collection[result.destination.droppableId].eventOrder,
                    result.source.index,
                    result.destination.index
                );

                if (!arrayEquals(this.state.itinerary.collection[result.destination.droppableId].eventOrder, newOrder)) {
                    let newItin = JSON.parse(JSON.stringify(this.state.itinerary));

                    newItin.collection[result.destination.droppableId].eventOrder = newOrder

                    this.setState({
                        itinerary: newItin
                    }, () => this.props.updateCurrentItinerary(this.state.itinerary));
                }
            } else {
                let newItin = JSON.parse(JSON.stringify(this.state.itinerary));
                let id = newItin.collection[result.source.droppableId].eventOrder[result.source.index]
                let item = this.state.itinerary.collection[result.source.droppableId].events[id]
                delete newItin.collection[result.source.droppableId].events[id];

                newItin.collection[result.source.droppableId].eventOrder.splice(result.source.index, 1)
                newItin.collection[result.destination.droppableId].events[id] = item
                newItin.collection[result.destination.droppableId].eventOrder = addToArray(newItin.collection[result.destination.droppableId].eventOrder, result.destination.index, id)

                this.setState({
                    itinerary: newItin
                }, () => this.props.updateCurrentItinerary(this.state.itinerary));
            }
        }
        this.setState({ dragging: false })
    }

    onDragStart = (e) => {
        if (e.source.droppableId !== "grid") {
            this.setState({ dragging: true })
        }
    };

    onDeleteClick = (id) => {
        let currentItem = this.state.item
        let that = this
        confirm({
            title: "Are you sure you want to delete this day?",
            okText: 'Yes',
            cancelText: 'No',
            onOk() {
                that.props.deleteSpecificCollection(id)
            },
            onCancel() {
                return false;
            },
        });
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.itinerary != this.state.itinerary) {
            this.setState({
                itinerary: nextProps.itinerary,
            })
        }
    }

    render() {
        return (
            <div className={multilist} style={{ position: "relative", top: 0, left: 0, width: "100%", maxHeight: "100%", minHeight: "100%" }}>
                <DragDropContext onDragEnd={this.onDragEnd} onDragStart={this.onDragStart}>
                    <Droppable direction="horizontal" droppableId={"grid"} type={"board"}>
                        {(provided, snapshot) => (
                            <div
                                className={multilist}
                                style={{ display: "flex", overflowX: "scroll", height: "100%", maxHeight: "100%", flexWrap: "nowrap" }}
                                ref={provided.innerRef}
                            >
                                {
                                    this.state.itinerary && this.state.itinerary.collectionOrder && this.state.itinerary.collectionOrder.map((collection, index) => {
                                        return (
                                            <Draggable draggableId={collection} index={index} key={collection} >
                                                {(provided, snapshot) => (
                                                    <div
                                                        style={{ display: "inline-flex", margin: "15px" }}
                                                        ref={provided.innerRef}
                                                        {...provided.draggableProps}
                                                        {...provided.dragHandleProps}
                                                    >
                                                        <Card title={<div><div style={{ maxWidth: "85%", overflow: "hidden", textOverflow: "ellipsis", float: "left" }}><span style={{ maxWidth: "150px" }}>Day {index+1}: {this.state.itinerary.collection[collection].name}</span></div><Button onClick={() => this.onDeleteClick(collection)} style={{ border: "none", boxShadow: "none", float: "right" }} icon={"close"} /></div>} bordered={false} style={{ margin: "15px", minWidth: "300px", maxWidth: "300px", boxShadow: "0 2px 8px rgba(0, 0, 0, 0.09)", borderColor: "rgba(0, 0, 0, 0.09)" }}>
                                                            <SingleDayList dragging={this.state.dragging} id={collection} collection={this.state.itinerary.collection[collection]} />
                                                        </Card>
                                                    </div>
                                                )
                                                }
                                            </Draggable>
                                        )
                                    })
                                }
                                {provided.placeholder}
                                <Button onClick={() => this.props.addCollectionToCurrentItinerary()} type={"primary"} style={{ margin: "15px", minWidth: "300px", maxWidth: "300px", boxShadow: "0 2px 8px rgba(0, 0, 0, 0.09)", borderColor: "rgba(0, 0, 0, 0.09)" }}>
                                    <Icon type="plus" />Add a Day
                                </Button>
                            </div>)}

                    </Droppable>
                </DragDropContext>
            </div >
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        updateCollectionOrder: (order) => dispatch(updateCollectionOrder(order)),
        updateCurrentItinerary: (itin) => dispatch(updateCurrentItinerary(itin)),
        addCollectionToCurrentItinerary: () => dispatch(addCollectionToCurrentItinerary()),
        deleteSpecificCollection: (id) => dispatch(deleteSpecificCollection(id))
    }
}

export default connect(null, mapDispatchToProps)(MultidayList)