const initState = {
    itinerary: null,
    currentCollectionId: null,
    loading: false,
}

const editReducer = (state = initState, action) => {
    switch (action.type) {
        case "START_TRANSACTION":
            return {
                ...state,
                loading: true
            }
        case "LOAD_ITINERARY_TO_VIEW":
            return {
                ...state,
                itinerary: action.itinerary,
                currentCollectionId: action.currentCollectionId,
                loading: false
            }
        case "UPDATE_COLLECTION_VIEWING":
            return {
                ...state,
                currentCollectionId: action.currentCollectionId,
            }
        case "CLEAR_ITINERARY_TO_VIEW":
            return {
                ...initState
            }
        default:
            return state;
    }
}

export default editReducer 