import { Icon, Spin, Tabs } from 'antd';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getPublicQueryResults } from '../../../store/actions/queryActions';
import { clearItineraryInView, loadItineraryToState } from '../../../store/actions/viewActions';
import ItineraryViewer from '../../ItineraryViewer';
import SearchInput from '../../SearchInput';
import ResultList from '../../Results/ResultList';
import FilterMenu from './../../Discover/FilterMenu';
import Map from './../../Map';
import { content, resultList, sticky } from './BuilderPane.module.scss';
import Search from './../../Search'
const TabPane = Tabs.TabPane;

class BuilderPane extends Component {

    handleSearch = (location) => {
        this.props.getPublicQueryResults({ lat: location.lat, lng: location.lng })
    }

    render() {

        const renderTabBar = (props, DefaultTabBar) => (
            <DefaultTabBar {...props} style={{ zIndex: 1000, position: 'relative', margin: 0, top: 0, left: 0, width: "100%", background: '#fff' }} />
        );

        return (
            <div style={{ height: "100%" }}>
                <Tabs defaultActiveKey="1" renderTabBar={renderTabBar}>
                    <TabPane className={content} tab={<span><Icon type="search" />Search</span>} key="1">
                        <Search />
                    </TabPane>
                    <TabPane className={content} tab={<span><Icon type="compass" />Discover</span>} key="2">
                        {
                            this.props.itineraryInFocus &&
                            <ItineraryViewer dragging={this.props.dragging} match={this.props.match} onBack={() => this.props.clearItineraryInView()}></ItineraryViewer>
                        }
                        {
                            !this.props.itineraryInFocus &&
                            <div>
                                <div className={sticky}>
                                    <div style={{ width: "100%", paddingTop: 10, paddingLeft: "15px", paddingRight: "15px" }}>
                                        <SearchInput reroute={false}></SearchInput>
                                    </div>
                                    <div style={{ width: "100%" }}>
                                        <FilterMenu drawer={false} />
                                    </div>
                                </div>
                                <div className={resultList}>
                                    <ResultList></ResultList>
                                </div>
                            </div>
                        }
                        {
                            this.props.loading &&
                            <Spin size="large" />
                        }
                    </TabPane>
                    <TabPane className={content} tab={<span><Icon type="heart" />Saved</span>} key="3">
                        Tab 3
                    </TabPane>
                    <TabPane className={content} tab={<span><Icon type="environment" />Map</span>} key="4">
                        <Map markers={this.props.markers}></Map>
                    </TabPane>
                </Tabs>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    auth: state.firebase.auth,
    itineraryInFocus: state.view.itinerary,
    loading: state.view.loading
})

const mapDispatchToProps = (dispatch) => {
    return {
        getPublicQueryResults: (coordinates) => dispatch(getPublicQueryResults(coordinates)),
        loadItineraryToState: () => dispatch(loadItineraryToState("3H0jUKxaJ5mX0LReiGH1")),
        clearItineraryInView: () => dispatch(clearItineraryInView())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(BuilderPane);
