import React, { Component } from 'react'
import { siderLayout, hover, hidden } from './DayMenu.module.scss'
import { Layout, Input, Modal, Row, Col, Icon, Button } from 'antd'
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { addCollectionToCurrentItinerary, editNameSpecificCollection, updateCollectionOrder, deleteSpecificCollection, updateCurrentCollectionSelected } from './../../../store/actions/editActions'
import { connect } from 'react-redux';
import { arrayEquals } from "./../../Core/Utilities"

const confirm = Modal.confirm;

const { Sider } = Layout;

const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);

    return result;
};

class DayMenu extends Component {

    constructor(props) {
        super(props);
        this.state = {
            collectionOrder: props.itinerary ? props.itinerary.collectionOrder : [],
            editModal: false,
            editTitle: "Day 1:",
            editId: "some id",
            editName: "some name"
        }
    }

    onDragEnd = (result) => {
        if (!result.destination) {
            return;
        }

        const items = reorder(
            this.props.itinerary.collectionOrder,
            result.source.index,
            result.destination.index
        );

        if (!arrayEquals(this.state.collectionOrder, items)) {
            this.setState({ collectionOrder: items })
            this.props.updateCollectionOrder(items);
        }
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.itinerary && this.props.itinerary.collectionOrder && this.props.itinerary.collectionOrder !== this.state.collectionOrder) {
            this.setState({ collectionOrder: this.props.itinerary.collectionOrder });
        }
        // triggers initial selection to be made if empty
        if (this.props.itinerary && !this.props.itinerary.collection[this.props.selected]) {
            this.props.updateCurrentCollectionSelected(this.props.itinerary.collectionOrder[0])
        }
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.itinerary && nextProps.itinerary.collectionOrder && nextProps.itinerary.collectionOrder !== this.state.collectionOrder) {
            this.setState({ collectionOrder: nextProps.itinerary.collectionOrder });
        }
        // triggers initial selection to be made if empty
        if (nextProps.itinerary && !nextProps.selected) {
            this.props.updateCurrentCollectionSelected(nextProps.itinerary.collectionOrder[0])
        }
    }

    onDeleteClick = (id) => {
        let currentItem = this.state.item
        let that = this
        confirm({
            title: "Are you sure you want to delete this day?",
            okText: 'Yes',
            cancelText: 'No',
            onOk() {
                that.props.deleteSpecificCollection(id)
            },
            onCancel() {
                return false;
            },
        });
    }

    onEditClick = (id, title, current) => {
        this.setState({ editModal: true, editTitle: title, editId: id, editName: current })
    }

    handleCancel = () => {
        this.setState({ editModal: false })
    }

    onEditOk = () => {
        this.setState({ editModal: false }, () => {
            this.props.editNameSpecificCollection(this.state.editId, this.state.editName)
        })
    }

    onChange = (e) => {
        this.setState({ editName: e.target.value })
    };


    render() {
        return (
            <Sider className={siderLayout} >
                <div style={{ position: "sticky", top: 0, width: "100%", backgroundColor: "white" }}>
                    <Button onClick={() => this.props.addCollectionToCurrentItinerary()} type="primary" style={{ position: "sticky", top: "1em", width: "90%", marginRight: "5%", marginLeft: "5%", marginTop: "1em", marginBottom: "1em" }}><Icon type="plus" />Add a Day</Button>
                </div>
                <DragDropContext onDragEnd={this.onDragEnd}>
                    <Droppable droppableId="droppable">
                        {(provided, snapshot) => (
                            < div ref={provided.innerRef}>
                                <ul className={"ant-menu ant-menu-light ant-menu-root ant-menu-inline"} style={{ borderRight: 0 }}>
                                    {this.state.collectionOrder.map((collectID, index) =>
                                        <Draggable key={collectID} draggableId={collectID} index={index}>
                                            {(provided, snapshot) => (
                                                <li ref={provided.innerRef}
                                                    {...provided.draggableProps}
                                                    {...provided.dragHandleProps}
                                                    className={this.props.selected === collectID ? "ant-menu-item ant-menu-item-selected" : "ant-menu-item"}
                                                    onClick={() => this.props.updateCurrentCollectionSelected(collectID)} key={index} >
                                                    <Row type="flex" justify="space-between" align="middle" className={hover}>
                                                        <Col span={16} style={{ flexGrow: 1, maxWidth: "170px", overflow: "hidden", textOverflow: "ellipsis" }}>
                                                            Day {index + 1}: {this.props.itinerary && this.props.itinerary.collection && this.props.itinerary.collection[collectID] && this.props.itinerary.collection[collectID].name ? this.props.itinerary.collection[collectID].name : "Untitled"}
                                                        </Col>
                                                        <Col span={8} className={hidden} >
                                                            <Button type="danger" ghost onClick={() => this.onDeleteClick(collectID)} style={{ boxShadow: "none", border: "none", float: "right", padding: 0 }} ><Icon style={{ margin: 0 }} type="close" /></Button>
                                                            <Button type="primary" ghost onClick={() => this.onEditClick(collectID, "Day " + (index + 1) + ":", this.props.itinerary.collection[collectID].name)} style={{ boxShadow: "none", border: "none", float: "right", padding: 0 }} ><Icon style={{ marginRight: 6 }} type="edit" /></Button>
                                                        </Col>
                                                    </Row>
                                                </li>
                                            )}
                                        </Draggable>
                                    )}
                                    {provided.placeholder}
                                </ul>
                            </div>
                        )}
                    </Droppable>
                </DragDropContext >
                <Modal
                    title={this.state.editTitle}
                    visible={this.state.editModal}
                    onOk={this.onEditOk}
                    onCancel={this.handleCancel}
                >
                    <Input value={this.state.editName} allowClear onChange={this.onChange} />
                </Modal>
            </Sider >
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        addCollectionToCurrentItinerary: () => dispatch(addCollectionToCurrentItinerary()),
        updateCollectionOrder: (order) => dispatch(updateCollectionOrder(order)),
        updateCurrentCollectionSelected: (id) => dispatch(updateCurrentCollectionSelected(id)),
        deleteSpecificCollection: (id) => dispatch(deleteSpecificCollection(id)),
        editNameSpecificCollection: (id, name) => dispatch(editNameSpecificCollection(id, name))
    }
}

const mapStateToProps = (state) => ({
    selected: state.edit.currentCollection
})

export default connect(mapStateToProps, mapDispatchToProps)(DayMenu)