import React, { Component } from 'react'
import { Row, Col } from 'antd'
import { input } from './Panel.module.scss'

export default class NamePanel extends Component {
  render() {
    return (
      <Row style={{ height: "100%", width:"100%"}} type="flex" justify="center" align="middle">
        <Col span={24}>
          <h1 style={{ marginBottom: "20px" }}>Yay! You're going on a trip! Let's give it a name 🤗</h1>
          <input value={this.props.name !== "Untitled" ? this.props.name : "" } onChange={this.props.onChange} placeholder="Untitled Trip Name 🏖️ 🏕  🏙️️" className={input}></input>
        </Col>
      </Row>
    )
  }
}
