import React, { Component } from 'react';
import { DragDropContext } from 'react-beautiful-dnd';
import PanelGroup from 'react-panelgroup';
import { connect } from 'react-redux';
import { firestoreConnect } from 'react-redux-firebase';
import { compose } from 'redux';
import ResizeContainer from '../Utilities/ResizeContainer';
import { loadItineraryToEdit, makeItineraryPublic, prepEditState, prepCurrentEventEdit, addEventToCurrentCollection, updateCurrentCollectionSelected, resetNewModal, updateEventOrder } from './../../store/actions/editActions';
import SvgKangaroo from './../Auth/SvgKangaroo';
import NavLayout from './../Core/NavLayout';
import BuilderPane from './BuilderPane';
import CreateModal from './CreateModal';
import DayMenu from './DayMenu';
import { overlay, panel, splitPane, footer, content } from './Edit.module.scss';
import EventList from './EventList';
import { Button, Row, Col, Spin, Divider, Radio, Icon } from 'antd'
import { guidGenerator } from './../Core/Utilities'
import MultidayList from './MultidayList';
import { arrayEquals } from "./../Core/Utilities"
import DetailsModal from './DetailsModal'


const antIcon = <Icon type="loading" style={{ fontSize: ".7em", color: "white" }} spin />;
const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;

const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);

    return result;
};

const addToArray = (list, index, element) => {
    const result = Array.from(list)
    result.splice(index, 0, element);
    return result;
}

class Edit extends Component {

    constructor(props) {
        super(props);

        let itinerary = props.itinerary ? props.itinerary[props.match.params.id] : {}

        this.state = {
            itinerary: itinerary,
            collectionID: props.collectionID ? props.collectionID : null,
            events: itinerary && props.collectionID && itinerary.collection[props.collectionID] ? itinerary.collection[props.collectionID].events : {},
            eventOrder: itinerary && props.collectionID && itinerary.collection[props.collectionID] ? itinerary.collection[props.collectionID].eventOrder : [],
            panelBclass: "XS",
            modal: this.props.edit.showNewModal,
            view: "single",
            detailsModal: false
        };
    }

    componentDidUpdate(prevProps) {
        let itinerary = this.props.itinerary && this.props.itinerary[this.props.match.params.id] ? this.props.itinerary[this.props.match.params.id] : null
        let prev = prevProps.itinerary && prevProps.itinerary[this.props.match.params.id] ? prevProps.itinerary[this.props.match.params.id] : null

        if ((itinerary && (JSON.stringify(itinerary) !== JSON.stringify(prev))) || (itinerary && (JSON.stringify(itinerary) !== JSON.stringify(this.state.itinerary))) || (itinerary && (this.props.collectionID !== this.state.collectionID))) {
            this.setState({
                itinerary: itinerary,
                detailsModal: false,
                collectionID: this.props.collectionID,
                events: itinerary && this.props.collectionID && itinerary.collection && itinerary.collection[this.props.collectionID] ? itinerary.collection[this.props.collectionID].events : {},
                eventOrder: itinerary && this.props.collectionID && itinerary.collection && itinerary.collection[this.props.collectionID] ? itinerary.collection[this.props.collectionID].eventOrder : [],
            })
        }
    }

    handleModal = () => {
        this.props.resetNewModal();
    }

    componentDidMount() {
        this.props.prepEditState(this.props.match.params.id);
    }

    onDragEnd = (result) => {
        if (result.destination) {
            if (result.source.droppableId === "editPanel" && result.destination.droppableId === "editPanel") {
                const newOrder = reorder(
                    this.state.eventOrder,
                    result.source.index,
                    result.destination.index
                );

                if (!arrayEquals(this.state.eventOrder, newOrder)) {
                    this.setState({
                        dragging: false,
                        eventOrder: newOrder,
                    }, () => this.props.updateEventOrder(newOrder));
                }
            }

            if (result.source.droppableId === "viewPanel" && result.destination.droppableId === "editPanel") {

                let eventId = this.props.view.itinerary.collection[this.props.view.currentCollectionId].eventOrder[result.source.index]
                let newEvent = this.props.view.itinerary.collection[this.props.view.currentCollectionId].events[eventId]

                newEvent.reference = {
                    itineraryID: this.props.view.itinerary.id,
                    collectionID: this.props.view.currentCollectionId,
                    eventID: eventId
                }

                let guid = guidGenerator()

                const newOrder = addToArray(
                    this.state.eventOrder,
                    result.destination.index,
                    guid
                )

                if (!arrayEquals(this.state.eventOrder, newOrder)) {
                    this.setState({
                        dragging: false,
                        eventOrder: newOrder,
                        events: { ...this.state.events, [guid]: newEvent }
                    }, () => this.props.addEventToCurrentCollection(guid, newOrder, newEvent));
                }
            }

            if (result.source.droppableId === "searchResults" && result.destination.droppableId === "editPanel") {
                console.log("hit")
                let details = this.props.searchResults[result.source.index];
                let guid = guidGenerator()

                let newEvent = {
                    name: details.name,
                    location: {
                        lat: details.geometry.location.lat(),
                        lng: details.geometry.location.lng(),
                        place_id: details.place_id,
                        name: details.formatted_address
                    }
                }

                const newOrder = addToArray(
                    this.state.eventOrder,
                    result.destination.index,
                    guid
                )

                if (!arrayEquals(this.state.eventOrder, newOrder)) {
                    this.setState({
                        dragging: false,
                        eventOrder: newOrder,
                        events: { ...this.state.events, [guid]: newEvent }
                    }, () => this.props.addEventToCurrentCollection(guid, newOrder, newEvent));
                }

                console.log(this.props.searchResults[result.source.index])
            }
        }
    }

    onDragStart = () => {
        this.setState({ dragging: true })
    };

    onChange = (e) => {
        this.setState({ view: e.target.value }, () => {
            this.props.prepCurrentEventEdit()
        })
    }

    render() {

        let eventCoords = []
        this.state.eventOrder.forEach((element) => {
            if (this.state.events[element] && this.state.events[element].location && this.state.events[element].location.lat && this.state.events[element].location.lng) {
                eventCoords.push(this.state.events[element].location)
            }
        })

        let itinerary = this.props.itinerary && this.props.itinerary[this.props.match.params.id] ? this.props.itinerary[this.props.match.params.id] : null
        return (
            <div>
                <NavLayout />
                <Button onClick={() => this.props.makeItineraryPublic()}>MAKE PUBLIC?</Button>

                {this.state.view === "multiple" &&
                    <div className={content}>
                        <MultidayList itinerary={itinerary} />
                    </div>
                }


                {this.state.view === "single" &&
                    <div>
                        <DayMenu itinerary={itinerary} />
                        <DragDropContext onDragEnd={this.onDragEnd} onDragStart={this.onDragStart}>
                            <div className={splitPane}>
                                <PanelGroup borderColor="#e8e8e8">
                                    <div className={panel} style={{ width: "100%", padding: "1.5em" }}>
                                        <SvgKangaroo className={overlay} />
                                        {this.props.itinerary && <EventList collectionID={this.state.collectionID} viewOnly={false} dragging={this.state.dragging} events={this.state.events} eventOrder={this.state.eventOrder}></EventList>}
                                        {!this.props.itinerary &&
                                            <Divider style={{ marginTop: "25vh", marginBottom: 5 }}>
                                                <Spin style={{}} size="large" />
                                            </Divider>
                                        }
                                    </div>
                                    <div className={panel}>
                                        <ResizeContainer>
                                            <BuilderPane markers={eventCoords} dragging={this.state.dragging} match={this.props.match}></BuilderPane>
                                        </ResizeContainer>
                                    </div>
                                </PanelGroup>
                            </div>
                        </DragDropContext>
                    </div>
                }
                <div className={footer}>
                    <Row type="flex" align="middle" style={{ height: "100%" }}>
                        <Col span={20}>
                            <RadioGroup style={{ marginLeft: "1em" }} onChange={this.onChange} defaultValue="single">
                                <RadioButton value="single"><Icon type="profile" /></RadioButton>
                                <RadioButton value="multiple"><Icon type="project" /></RadioButton>
                            </RadioGroup>
                            <span style={{ fontSize: "20px", marginLeft: "1em", color: "white" }}>{itinerary && itinerary.name ? itinerary.name : "Untitled"}</span>
                            <Icon type="form" style={{ color: "white", marginLeft: "1em", fontSize: "16px" }} onClick={() => this.setState({detailsModal: true})}/>
                        </Col>
                        <Col span={4} style={{ textAlign: "right", paddingRight: "1em" }}>
                            {this.props.saving && <span style={{ fontSize: "1em", color: "white" }}>Saving... <Spin indicator={antIcon} /></span>}
                            {!this.props.saving && <span style={{ fontSize: "1em", color: "white" }}>Saved <Icon type="check" style={{ color: "white", fontSize: ".7em" }} /></span>}
                        </Col>
                    </Row>
                </div>
                <CreateModal visible={this.props.edit.showNewModal} handleModal={() => this.handleModal()} itinerary={itinerary}></CreateModal>
                <DetailsModal visible={this.state.detailsModal} closeDetailModal={() => this.setState({detailsModal: false})} itinerary={itinerary}></DetailsModal>
            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        ...ownProps,
        edit: state.edit,
        auth: state.firebase.auth,
        itinerary: state.firestore.data['users/' + state.firebase.auth.uid + '/itineraries'],
        collectionID: state.edit.currentCollection,
        view: state.view,
        saving: state.edit.saving,
        searchResults: state.search.results
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        makeItineraryPublic: () => dispatch(makeItineraryPublic()),
        loadItineraryToEdit: (id) => dispatch(loadItineraryToEdit(id)),
        updateEventOrder: (newOrder) => dispatch(updateEventOrder(newOrder)),
        prepEditState: (id) => dispatch(prepEditState(id)),
        resetNewModal: () => dispatch(resetNewModal()),
        updateCurrentCollectionSelected: (id) => dispatch(updateCurrentCollectionSelected(id)),
        addEventToCurrentCollection: (id, reorder, event) => dispatch(addEventToCurrentCollection(id, reorder, event)),
        prepCurrentEventEdit: () => dispatch(prepCurrentEventEdit(null, null))
    }
}

export default compose(
    connect(mapStateToProps, mapDispatchToProps),
    firestoreConnect(props => [{
        collection: 'users/' + props.auth.uid + '/itineraries',
        doc: props.match.params.id
    }])
)(Edit)