import React, { Component } from 'react'
import { Menu, Popover, Icon } from 'antd';
import { connect } from 'react-redux';
import { signOut } from '../../../store/actions/authActions'
import { menuStyle } from './ProfileDrop.module.scss'


class ProfileDrop extends Component {
    render() {
        const menu = (
            <Menu className={menuStyle}>
                {this.props.auth.displayName &&
                    <Menu.Item disabled >
                        Signed In As: {this.props.auth.displayName}
                    </Menu.Item>
                }
                <Menu.Item>
                    <Icon type="user" />Profile
                </Menu.Item>
                <Menu.Item>
                    <Icon type="setting" />Settings
                </Menu.Item>
                <Menu.Item onClick={() => this.props.signOut()}>
                    <Icon type="logout" />Sign Out
                </Menu.Item>
            </Menu>
        );

        return (
            <Popover content={menu} placement="bottomRight">
                {this.props.children}
            </Popover>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        signOut: () => dispatch(signOut())
    }
}

export default connect(null, mapDispatchToProps)(ProfileDrop)

