const initState = {
    formattedResults: null,
    loading: false,
    specificLoading: false,
    discoverModalOpen: false,
    lastSnapshot: null,
}

const queryReducer = (state = initState, action) => {
    switch (action.type) {
        case 'QUERY_SUCCESS':
            return {
                ...state,
                formattedResults: action.formattedResults,
                currentQuery: action.currentQuery ? action.currentQuery : state.currentQuery,
                lastSnapshot: action.lastSnapshot,
                loading: false
            }
        case 'QUERY_LOADING':
            return {
                ...state,
                formattedResults: null,
                loading: true
            }
        case 'SPECIFIC_QUERY_SUCCESS':
            return {
                ...state,
                focused: action.focused,
                specificLoading: false
            }
        case 'SPECIFIC_QUERY_ERROR':
            return {
                ...state,
                focused: null,
                specificLoading: false
            }
        case 'SPECIFIC_QUERY_LOADING':
            return {
                ...state,
                focused: null,
                discoverModalOpen: true,
                specificLoading: true
            }
        case 'CLOSE_DISCOVER_MODAL':
            return {
                ...state,
                discoverModalOpen: false
            }
        default:
            return state;
    }
}

export default queryReducer