import React, { Component } from 'react'
import { GOOGLE_PLACES_APIKEY } from './../../config/placesConfig'
import { Button, Empty, Input, Row, Col, Carousel, Card, Icon } from 'antd'
import { Droppable, DragDropContext, Draggable } from 'react-beautiful-dnd';
import { input, container, containerNoBorder } from './Search.module.scss'
import { updateSearchResults } from './../../store/actions/searchActions'
import SearchResult from './SearchResult'
import { connect } from 'react-redux'
import google from './static/powered_by_google_on_white_hdpi.png'
const SearchInput = Input.Search;

const QUERYURL = 'https://maps.googleapis.com/maps/api/place/findplacefromtext/json?'
const service = new window.google.maps.places.PlacesService();

class Search extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            results: [],
            query: "",
            pagination: null
        }
    }

    searchResults = () => {
        this.setState({ loading: true, results: [] }, () => {
            var request = {
                query: this.state.query
            };

            if (this.props.lat && this.props.lng) {
                let latlng = new window.google.maps.LatLng({ lat: this.props.lat, lng: this.props.lng });
                request['location'] = latlng
            }

            var service = new window.google.maps.places.PlacesService(document.createElement('div'));

            service.textSearch(request, (results, status, pagination) => {
                if (status === window.google.maps.places.PlacesServiceStatus.OK) {
                    this.setState({ results: this.state.results.concat(results), loading: false, pagination: pagination }, () => this.props.updateSearchResults(this.state.results))
                } else {
                    this.setState({ loading: false })
                }
            });
        })
    }

    onQueryChange = (e) => {
        this.setState({ query: e.target.value })
    }

    render() {

        let show = this.state.results && this.state.results.map((result, index) => {
            return (
                <Draggable draggableId={"search-" + index} index={index} key={"search-" + index} >
                    {(provided, snapshot) => (
                        <div
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                        >
                            <SearchResult result={result} />
                        </div>)}
                </Draggable>
            )
        })

        return (
            <div>
                <div className={container}>
                    <SearchInput
                        className={input}
                        onChange={this.onQueryChange}
                        placeholder="Search"
                        onSearch={this.searchResults}
                        enterButton={<Button type="primary" size="large" disabled={this.state.query.length < 1} icon="search" />}
                    />
                    <img style={{ paddingTop: 5, paddingBottom: 5, width: "140px", float: "right" }} src={google} />
                </div>
                <div className={containerNoBorder}>

                    <Droppable droppableId={"searchResults"} isDropDisabled={true}>
                        {(provided, snapshot) => (
                            <div ref={provided.innerRef}>
                                {show}
                            </div>
                        )}
                    </Droppable>

                    {
                        !this.state.results || this.state.results.length < 1 &&
                        <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} description="No Results" />
                    }

                    {this.state.results.length > 0 &&
                        <div style={{ textAlign: "center" }}>
                            <Button onClick={() => { this.state.pagination.nextPage() }} disabled={!this.state.pagination || (this.state.pagination && !this.state.pagination.hasNextPage)} >Load More Results</Button>
                        </div>
                    }
                </div>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        updateSearchResults: (results) => dispatch(updateSearchResults(results))
    }
}

export default connect(null, mapDispatchToProps)(Search)