import React, { Component } from 'react'
import {
    Table, Input, Button, Icon, Tag
} from 'antd';
import Moment from 'react-moment';
import moment from 'moment'
import Highlighter from 'react-highlight-words';
import { withRouter } from "react-router";


class ItineraryTable extends Component {

    state = {
        searchText: '',
    };

    handleChange = (pagination, filters, sorter) => {
        console.log('Various parameters', pagination, filters, sorter);
        this.setState({
            sortedInfo: sorter,
        });
    }


    getColumnSearchProps = (dataIndex) => ({
        filterDropdown: ({
            setSelectedKeys, selectedKeys, confirm, clearFilters,
        }) => (
                <div style={{ padding: 8 }}>
                    <Input
                        ref={node => { this.searchInput = node; }}
                        placeholder={`Search ${dataIndex}`}
                        value={selectedKeys[0]}
                        onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
                        onPressEnter={() => this.handleSearch(selectedKeys, confirm)}
                        style={{ width: 188, marginBottom: 8, display: 'block' }}
                    />
                    <Button
                        type="primary"
                        onClick={() => this.handleSearch(selectedKeys, confirm)}
                        icon="search"
                        size="small"
                        style={{ width: 90, marginRight: 8 }}
                    >
                        Search
              </Button>
                    <Button
                        onClick={() => this.handleReset(clearFilters)}
                        size="small"
                        style={{ width: 90 }}
                    >
                        Reset
              </Button>
                </div>
            ),
        filterIcon: filtered => <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />,
        onFilter: (value, record) => record[dataIndex].toString().toLowerCase().includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: (visible) => {
            if (visible) {
                setTimeout(() => this.searchInput.select());
            }
        },
        render: (text) => (
            <Highlighter
                highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
                searchWords={[this.state.searchText]}
                autoEscape
                textToHighlight={text.toString()}
            />
        ),
    })

    handleSearch = (selectedKeys, confirm) => {
        confirm();
        this.setState({ searchText: selectedKeys[0] });
    }

    handleReset = (clearFilters) => {
        clearFilters();
        this.setState({ searchText: '' });
    }

    render() {


        let { sortedInfo } = this.state;
        sortedInfo = sortedInfo || {};

        const columns = [{
            title: 'Name',
            dataIndex: 'name',
            key: 'name',
            width: '30%',
            sorter: (a, b) => a.name.length - b.name.length,
            sortOrder: sortedInfo.columnKey === 'name' && sortedInfo.order,
            ...this.getColumnSearchProps('name'),
        },
        {
            title: 'Tags',
            key: 'tags',
            dataIndex: 'tags',
            render: tags => (
                <span>
                    {tags.map(tag => {
                        let color = tag.length > 5 ? 'geekblue' : 'green';
                        if (tag === 'loser') {
                            color = 'volcano';
                        }
                        return <Tag color="#108ee9" key={tag}>{tag.toUpperCase()}</Tag>;
                    })}
                </span>
            ),
        },
        {
            title: 'Location',
            key: 'location',
            dataIndex: 'location',
            render: location => (
                <span>
                    {location.length > 0 ? location.map((loc,index) => {
                        return (
                            <span>
                                {index + 1 === location.length ? loc.name : loc.name + " | " }
                            </span>
                        )
                    }) : "N/A"}
                </span>
            ),
        },
        {
            title: 'Created',
            key: 'createdAt',
            dataIndex: 'createdAt',
            sorter: (a, b) => a.createdAtDate - b.createdAtDate,
            sortOrder: sortedInfo.columnKey === 'createdAt' && sortedInfo.order,
            render: createdAt => (
                <span>
                    {
                        moment().diff(createdAt.toDate(), 'days') != 0 &&
                        <Moment format="MM/DD/YYYY">{createdAt.toDate()}</Moment>
                    }
                    {
                        moment().diff(createdAt.toDate(), 'days') == 0 &&
                        <Moment fromNow>{createdAt.toDate()}</Moment>
                    }
                </span>
            ),
        },
        {
            title: 'Last Edited',
            key: 'editedAt',
            dataIndex: 'editedAt',
            sorter: (a, b) => a.editedAtDate - b.editedAtDate,
            sortOrder: sortedInfo.columnKey === 'editedAt' && sortedInfo.order,
            render: editedAt => (
                <span>
                    {
                        moment().diff(editedAt.toDate(), 'days') != 0 &&
                        <Moment format="MM/DD/YYYY">{editedAt.toDate()}</Moment>
                    }
                    {
                        moment().diff(editedAt.toDate(), 'days') == 0 &&
                        <Moment fromNow>{editedAt.toDate()}</Moment>
                    }
                </span>
            ),
        }
        ];
        return <Table onRow={(record, rowIndex) => {
            return {
                onClick: (event) => { this.props.history.push('/edit/' + record.id); }
            }
        }} loading={this.props.loading} pagination={false} columns={columns} dataSource={this.props.itineraries} onChange={this.handleChange} />;
    }
}


export default withRouter(ItineraryTable);