import { Avatar, Col, Icon, Menu, Row } from 'antd';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { menu, menuSider } from './AuthMenu.module.scss';
import ProfileDrop from './ProfileDrop';
import { createNewItinerary } from "./../../../store/actions/editActions"
const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;

class AuthMenu extends Component {

    render() {

        return (
            <div>
                {
                    !this.props.sider &&
                    <Menu selectable={false} className={this.props.sider ? menuSider : menu} mode={this.props.sider ? "vertical" : "horizontal"}>
                        <Menu.Item key="1" onClick={() => this.props.createNewItinerary()}>
                            <Icon type="plus" style={{ marginRight: '0px' }} />
                        </Menu.Item>
                        <Menu.Item key="2">
                            <Link to="/discover">Discover</Link>
                        </Menu.Item>
                        <Menu.Item key="3">
                            <Link to="/feed">Following</Link>
                        </Menu.Item>
                        <ProfileDrop auth={this.props.auth}>
                            <Link to="/profile" style={{ marginRight: '0px' }}>
                                {this.props.auth.photoURL && <Avatar src={this.props.auth.photoURL} />}
                                {!this.props.auth.photoURL && <Avatar style={{ marginRight: '0px' }} icon="user" />}
                            </Link>
                        </ProfileDrop>
                    </Menu>
                }
                {
                    this.props.sider &&

                    <Menu selectable={false} className={menuSider} mode={"vertical"}>
                        <Link to="/profile" >
                            <Row style={{ marginLeft: '14px' }} type="flex" justify="left" align="middle">
                                <Col>
                                    {this.props.auth.photoURL && <Avatar src={this.props.auth.photoURL} />}
                                    {!this.props.auth.photoURL && <Avatar style={{ marginRight: '0px' }} icon="user" />}
                                </Col>
                                <Col style={{ marginLeft: '14px' }}>
                                    {this.props.auth.displayName}
                                </Col>
                            </Row>
                        </Link>
                        <Menu.Item key="2">
                            <Link to="/discover">Discover</Link>
                        </Menu.Item>
                        <Menu.Item key="3">
                            <Link to="/feed">Following</Link>
                        </Menu.Item>
                        <Menu.Item key="1" onClick={() => this.props.createNewItinerary()}>
                            <Icon type="plus" style={{ marginRight: '0px' }} />
                        </Menu.Item>
                    </Menu>
                }
            </div>

        );
    }
}


const mapDispatchToProps = (dispatch) => {
    return {
        createNewItinerary: () => dispatch(createNewItinerary())
    }
}

export default connect(null, mapDispatchToProps)(AuthMenu); 