import React, { Component } from 'react'
import ResizeObserver from 'react-resize-observer';

const breakpoints = {
    XS: 480,
    SM: 576,
    MD: 768,
    LG: 992,
    XL: 1200,
    XXL: 1600,
}

export default class ResizeContainer extends Component {

    state = {
        class: "XS"
    }

    render() {
        return (
            <div style={{width: "100%", height:"100%"}} className={this.state.class}>
                <ResizeObserver
                    onResize={(rect) => {
                        let temp = ['XS'];
                        Object.keys(breakpoints).forEach(key => {
                            if (rect.width > breakpoints[key]) {
                                temp.push(key);
                            }
                        })
                        this.setState({ class: temp[temp.length - 1] })
                    }}
                />
                {this.props.children}
            </div>
        )
    }
}
