import React, { Component } from 'react'
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { Card, Row, Icon, Modal } from 'antd'
import { prepCurrentEventEditSpecific, addNewEventToSpecificCollection } from './../../../store/actions/editActions'
import { connect } from 'react-redux'
import { list, addSection } from './MultidayList.module.scss'
import { Button, Divider } from 'antd'
import EventItemEdit from '../EventList/EventItemEdit';
import { guidGenerator } from './../../Core/Utilities'

class SingleDayList extends Component {


    addSectionClick = (itemBefore) => {
        let guid = guidGenerator()
        let items = {
            ...this.props.collection.events,
            [guid]: { id: guid }
        }
        if (!itemBefore) {
            this.setState({ eventOrder: [guid], events: items }, () => this.props.addNewEventToSpecificCollection(this.props.id, guid, [guid]));
        } else {
            let index = this.props.collection.eventOrder.indexOf(itemBefore);
            let newOrder = [...this.props.collection.eventOrder].slice(0, index + 1).concat([guid]).concat([...this.props.collection.eventOrder].slice(index + 1))
            this.props.addNewEventToSpecificCollection(this.props.id, guid, newOrder);
        }
    }
    render() {
        return (
            <Droppable droppableId={this.props.id} isDropDisabled={this.props.viewOnly}>
                {(provided, snapshot) => (
                    <div
                        style={{ paddingTop: "1em", width: "100%", minHeight: "100px" }}
                        ref={provided.innerRef}
                        className={list}>
                        <div className={"ant-timeline"}>
                            {this.props.collection.eventOrder.length < 1 &&
                                <div onClick={() => this.addSectionClick()}>
                                    <Divider style={{ marginTop: "10%"}}>
                                        <Button type="primary" size="small" shape="circle" icon="plus" />
                                    </Divider>
                                </div>
                            }
                            {this.props.collection.eventOrder && this.props.collection.eventOrder.map((eventID, index) => (
                                <Draggable
                                    key={eventID}
                                    draggableId={eventID}
                                    index={index}
                                    type="DEFAULT"
                                >
                                    {(provided, snapshot) => (
                                        <div ref={provided.innerRef}
                                            {...provided.draggableProps}
                                            {...provided.dragHandleProps}>
                                            <li className={this.props.collection.eventOrder.length - 1 === index ? "ant-timeline-item ant-timeline-item-last" : "ant-timeline-item"}
                                                style={{ padding: 0 }}
                                            >
                                                {!this.props.dragging && <div className={"ant-timeline-item-tail"} />}
                                                {!this.props.dragging && <div className={"ant-timeline-item-head ant-timeline-item-head-blue"} />}
                                                <div className={"ant-timeline-item-content"} >
                                                    {
                                                        this.props.editing === eventID &&
                                                        <Modal
                                                            closable={false}
                                                            footer={null}
                                                            visible={this.props.editing === eventID}
                                                            centered
                                                            width="50vw"
                                                        >
                                                            <EventItemEdit item={this.props.collection.events[eventID]} />
                                                        </Modal>
                                                    }
                                                    <div onClick={() => { this.props.prepCurrentEventEditSpecific(this.props.id, eventID, this.props.collection.events[eventID]) }}>
                                                        <EventItemMini item={this.props.collection.events[eventID]} />
                                                    </div>
                                                    <div onClick={() => this.addSectionClick(eventID)} className={addSection}>
                                                        <Divider style={{ marginTop: 0, marginBottom: 0 }}>
                                                            <Button type="primary" size="small" shape="circle" icon="plus" />
                                                        </Divider>
                                                    </div>
                                                </div>
                                            </li>
                                        </div>
                                    )}
                                </Draggable>
                            ))}
                            {provided.placeholder}
                        </div>
                        
                    </div>
                )}
            </Droppable>
        )
    }
}

class EventItemMini extends Component {
    render() {
        return (
            <Card bordered={true}>
                <Row type="flex" justify="start">
                    {this.props.item.name}
                    {
                        this.props.item.location &&
                        <div>
                            <Icon type="environment" style={{ marginRight: "10px" }} /><span style={{ color: "#1890ff" }}>{this.props.item.location.name}</span>
                        </div>
                    }
                </Row>
            </Card>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        prepCurrentEventEditSpecific: (collecId, id, event) => dispatch(prepCurrentEventEditSpecific(collecId, id, event)),
        addNewEventToSpecificCollection: (collecId, guid, newOrder) => dispatch(addNewEventToSpecificCollection(collecId, guid, newOrder))
    }
}

const mapStateToProps = (state) => ({
    editing: state.edit.editEventID
})

export default connect(mapStateToProps, mapDispatchToProps)(SingleDayList)