import { Alert, Button, Checkbox, Form, Icon, Input } from 'antd';
import React, { Component } from 'react';
import { Icon as Icon2 } from 'react-icons-kit';
import { facebook, google } from 'react-icons-kit/fa';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { signInWithEmailAndPassword, signInWithFacebook, signInWithGoogle } from './../../store/actions/authActions';
import { btn, btn1, divider, icon } from './LoginForm.module.scss';


class LoginForm extends Component {

    constructor(props) {
        super(props);
        this.state = {
            confirmDirty: false,
            autoCompleteResult: [],
            credentials: {
                email: null,
                password: null,
            }
        };
    }

    handleChange = (e) => {
        console.log(this.state)
        let userCreds = {
            ...this.state.credentials
        }
        userCreds[e.target.id] = e.target.value

        this.setState({
            credentials: userCreds
        });
    }

    handleSubmit = (e) => {
        e.preventDefault()

        this.props.form.validateFields((err, values) => {
            this.props.signInWithEmailAndPassword(this.state.credentials)
        });
    }

    render() {
        const { getFieldDecorator } = this.props.form;

        return (
            <div>
                <Button className={[btn1, btn].join(' ')} shape="round" type="primary" onClick={() => this.props.signInWithGoogle()}><Icon2 size={14} className={icon} icon={google}></Icon2>Login with Google</Button>
                <Button shape="round" type="primary" onClick={() => this.props.signInWithFacebook()}><Icon2 size={14} className={icon} icon={facebook}></Icon2>Login with Facebook</Button>
                <p className={divider}>- Or -</p>
                {this.props.authError && <Alert type="error" message={this.props.authError.message} banner />}
                <Form onSubmit={this.handleSubmit} className="login-form">
                    <Form.Item>
                        {getFieldDecorator('email', {
                            rules: [{
                                type: 'email', message: 'Please enter a valid email.',
                            }, {
                                required: true, message: 'Please enter an email.',
                            }],
                        })(
                            <Input id="email" onChange={this.handleChange} prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Email" />
                        )}
                    </Form.Item>
                    <Form.Item>
                        {getFieldDecorator('password', {
                            rules: [{ required: true, message: 'Password Required' }],
                        })(
                            <Input id="password" onChange={this.handleChange} prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="Password" />
                        )}
                    </Form.Item>
                    <Link className="login-form-forgot" to="">Forgot password?</Link>
                    <Form.Item>
                        {getFieldDecorator('remember', {
                            valuePropName: 'checked',
                            initialValue: true,
                        })(
                            <Checkbox>Remember me</Checkbox>
                        )}
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit" className="login-form-button">
                            Log in
                        </Button>
                    </Form.Item>
                    <Form.Item>
                        <span>
                            New to Travelroo.io? <Link className="login-form-forgot" to="/signup">Sign up now</Link>
                        </span>
                    </Form.Item>
                </Form>
            </div>
        );
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        signInWithGoogle: () => dispatch(signInWithGoogle()),
        signInWithFacebook: () => dispatch(signInWithFacebook()),
        signInWithEmailAndPassword: (creds) => dispatch(signInWithEmailAndPassword(creds))
    }
}

const mapStateToProps = (state) => ({
    authError: state.auth.authError
})

export default connect(mapStateToProps, mapDispatchToProps)(Form.create()(LoginForm));