import React, { Component } from 'react'
import { Alert, Modal, Input, Row, Col, Button } from 'antd'
import LocationInput from './LocationInput';
import TagGroup from './../EventList/TagGroup'
import { updateItineraryDetails } from './../../../store/actions/editActions'
import { connect } from 'react-redux'
import ReactQuill from 'react-quill';

class DetailsModal extends Component {

    constructor(props) {
        super(props);

        this.state = {
            errorMessage: "",
            locations: this.props.itinerary && this.props.itinerary.location && this.props.itinerary.location.length > 0 ? this.props.itinerary.location : [{}],
            name: this.props.itinerary && this.props.itinerary.name ? this.props.itinerary.name : "",
            tags: this.props.itinerary && this.props.itinerary.tags ? this.props.itinerary.tags : [],
            description: this.props.itinerary && this.props.itinerary.description ? this.props.itinerary.description : "",
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (JSON.stringify(prevProps.itinerary) !== JSON.stringify(this.props.itinerary)) {
            this.setState({
                errorMessage: "",
                locations: this.props.itinerary && this.props.itinerary.location && this.props.itinerary.location.length > 0 ? this.props.itinerary.location : [{}],
                name: this.props.itinerary && this.props.itinerary.name ? this.props.itinerary.name : "",
                tags: this.props.itinerary && this.props.itinerary.tags ? this.props.itinerary.tags : [],
                description: this.props.itinerary && this.props.itinerary.description ? this.props.itinerary.description : "",
            })
        }
    }

    handleDetailChange = (value) => {
        this.setState({ description: value });
    }

    handleTagChange = (tags) => {
        this.setState({ tags })
    }


    onChange = (field, value) => {
        this.setState({
            [field]: value
        })
    }

    onButtonClick = (e) => {
        this.setState({ locations: [...this.state.locations, {}] })
    }

    onButtonDeleteClick = (index) => {
        let newList = [...this.state.locations];
        newList.splice(index, 1)
        this.setState({ locations: newList })
    }

    handleLocation = (location, index) => {
        let newList = this.state.locations.slice(0, index).concat([location]).concat(this.state.locations.slice(index + 1));
        this.setState({ locations: newList })
    }

    purgeLocations = () => {
        let locations = [...this.state.locations]
        let purge = []

        locations.forEach((location) => {
            if (location.name) {
                purge.push(location)
            }
        })
        return purge
    }

    handleOk = async () => {
        let purge = this.purgeLocations()
        let errorMessage = "";

        if (this.state.name.trim().length < 1) {
            errorMessage = "Name field is required."
        }

        if (purge.length < 1) {
            errorMessage = errorMessage.length > 0 ? "Name field and 1 Location is required." : "1 Location is required."
        }

        if (errorMessage.length < 1) {
            await this.props.updateItineraryDetails(this.state.name.trim(), purge, this.state.description, this.state.tags)
            await this.props.closeDetailModal()
        } else {
            this.setState({ errorMessage: errorMessage })
        }
    }

    render() {
        return (
            <Modal
                visible={this.props.visible}
                centered
                width="50vw"
                closable={false}
                onOk={this.handleOk}
                title={"Itinerary Details"}
                onCancel={() => this.props.closeDetailModal()}
            >
                <div style={{ padding: "1em" }}>
                    {this.state.errorMessage.length > 0 && <Alert message={this.state.errorMessage} type="error" />}

                    <div style={{ marginTop: 10, marginBottom: 10 }}>
                        <h3>Name:</h3>
                        <Input value={this.state.name} id="name" onChange={(e) => this.onChange('name', e.target.value)} placeholder="Itinerary Name" />
                    </div>

                    <div style={{ marginTop: 10, marginBottom: 10 }}>
                        <h3>Description:</h3>
                        <ReactQuill style={{ height: "250px", marginBottom: '50px' }} value={this.state.description} onChange={this.handleDetailChange} />
                    </div>

                    <div style={{ marginTop: 10, marginBottom: 10 }}>
                        <h3>{this.state.locations.length > 1 ? "Locations:" : "Location:"}</h3>
                        {this.state.locations.map((location, index) => {
                            return (
                                <Row type="flex" justify="start" align="middle" style={{ marginBottom: "10px" }}>
                                    <Col style={{ display: 'inline-flex', flexGrow: 1 }}>
                                        <LocationInput location={location} handleLocationSelect={(location) => this.handleLocation(location, index)} />
                                    </Col>
                                    {this.state.locations.length > 1 &&
                                        <Col style={{ marginLeft: "10px", display: 'inline-flex' }}>
                                            <Button size="small" type="danger" ghost shape="circle" icon="minus" onClick={() => this.onButtonDeleteClick(index)} />
                                        </Col>
                                    }
                                </Row>
                            )
                        })}
                        <Button type="primary" onClick={this.onButtonClick}>Add Another Location</Button>
                    </div>

                    <div style={{ marginTop: 10, marginBottom: 10 }}>
                        <h3>{this.state.tags.length > 1 ? "Tags:" : "Tag"}</h3>
                        <TagGroup tags={this.state.tags} handleTagChange={(tags) => this.handleTagChange(tags)}></TagGroup>
                    </div>
                </div>
            </Modal>
        );
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        updateItineraryDetails: (name, location, description, tags) => dispatch(updateItineraryDetails(name, location, description, tags))
    }
}

export default connect(null, mapDispatchToProps)(DetailsModal)