import React, { Component } from 'react';
import { Router, Route, Switch } from 'react-router-dom';
import './App.scss';
import Auth from './components/Auth';
import Edit from './components/Edit';
import Dashboard from './components/Dashboard';
import Discover from './components/Discover';
import Home from './components/Home';
import AuthRoute from './components/Utilities/AuthRoute';
import TestPage from './components/TestPage';
import history from "./history";
import Profile from './components/Profile'

const Signup = () => <Auth isLogin={false} />
const Login = () => <Auth isLogin={true} />

export default class App extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <Router history={history} >
        <Switch>
          <Route path="/test" component={TestPage} />
          <Route path="/discover" component={Discover} />
          <AuthRoute isAuth={false} path="/signup" redirect="/home" component={Signup} />
          <AuthRoute isAuth={false} path="/login" redirect="/home" component={Login} />
          <AuthRoute isAuth={true} path="/profile" redirect="/home" component={Profile} />
          <AuthRoute isAuth={true} path="/home" redirect="/login" component={Dashboard} />
          <Route path="/edit/:id/:action?" component={Edit} />
          <AuthRoute isAuth={false} path="/" redirect="/profile" component={Home} />
        </Switch>
      </Router>
    );
  }
}